.professionals {
	margin-bottom: 50px;
	@include media('>=desktop') {
		margin-bottom: 100px;
	}
	@include media('>=large-desktop') {
		margin-bottom: 150px;
	}

	.more {
		padding-top: 20px;
		@include media('>=desktop') {
			padding-top: 42px;
		}
		@include media('>=large-desktop') {
			padding-top: 62px;
		}
	}

	.load-more {
		transition: transform .8s ease-in-out;

		.no-touch &:hover {
			transform: rotate(360deg);
		}
	}
}