.heading-with-button {
	color: $color-gray;
	@include media('>=tablet') {
		display: flex;
		align-items: center;
		justify-content: space-between;
		margin-bottom: 30px;
	}
	@include media('>=desktop') {
		margin-bottom: 40px;
		align-items: flex-end;
	}
	@include media('>=large-desktop') {
		margin-bottom: 70px;
	}

	h2, .h2, h3, .h3 {
		margin-bottom: 0;
	}

	p {
		margin-top: 10px;
		@include media('>=tablet') {
			max-width: 373px;
		}
		@include media('>=desktop') {
			font-size: 18px;
			line-height: 33px;
		}
		@include media('>=large-desktop') {
			//margin-top: 20px;
			max-width: 610px;
			font-size: 24px;
			line-height: 41px;
		}
	}

	.button {
		margin: 30px 0;
		@include media('>=tablet') {
			flex-shrink: 0;
			margin: 0 0 0 20px;
		}
	}
}