.article-details-heading {
	margin-bottom: 30px;
	@include media('>=desktop') {
		margin-bottom: 40px;
	}
	@include media('>=large-desktop') {
		margin-bottom: 50px;
	}
	.info {
		@include media('>=large-desktop') {
			max-width: 1050px;
		}
	}
	h3, .h3 {
		@include media('>=desktop') {
			margin-bottom: 30px;
		}
		@include media('>=large-desktop') {
			margin-bottom: 40px;
		}
	}
}

.article-tags {
	font-size: 14px;
	line-height: 18px;
	text-transform: uppercase;
	margin-bottom: 20px;
	@include media('>=large-desktop') {
		margin-bottom: 0;
	}
	li {
		display: inline-block;
		vertical-align: top;
		padding: 10px 20px;
		background: $color-gray-light;
		text-align: center;
		margin: 0 10px 15px 0;
		@include media('>=large-desktop') {
			margin-bottom: 20px;
		}
	}
}

.event-details-wrap {
	margin-bottom: 30px;
	@include media('>=tablet-landscape') {
		margin-bottom: 0;
	}
}

.event-details {
	padding: 20px;
	@include media('>=desktop') {
		padding: 30px 30px;
	}
	@include media('>=large-desktop') {
		padding-bottom: 25px;
	}
	background: $color-gray-light;
	h4, .h4 {
		color: $color-red-dark;
		border-bottom: 1px solid rgba($color-gray, .5);
		padding-bottom: 10px;
		@include media('>=tablet') {
			margin-bottom: 20px;
		}
		@include media('>=large-desktop') {
			padding-bottom: 8px;
		}
	}
	.table {
		margin-bottom: -20px;
		@include media('>=tablet') {
			display: flex;
			margin: 0 -10px;
		}
	}
	.title {
		text-transform: uppercase;
		color: $color-gray;
		margin-bottom: 10px;
		@include media('>=desktop') {
			margin-bottom: 3px;
		}
	}
	.date{
		@include media('>=large-desktop') {
			font-size: 20px;
			line-height: 24px;
		}
	}
	.cell {
		margin-bottom: 20px;
		position: relative;
		@include media('>=tablet') {
			width: 33.33%;
			padding: 0 15px;
		}
		@include media('>=desktop') {
			padding: 0 30px;
		}
		@include media('>=large-desktop') {
			padding: 0 50px;
		}
		&:before {
			@include media('>=tablet') {
				content: '';
				position: absolute;
				left: 0;
				top: 10px;
				bottom: 28px;
				width: 1px;
				background: $color-gray;
			}
		}
		&:first-child {
			@include media('>=tablet') {
				width: 22%;
			}
			@include media('>=desktop') {
				padding: 0 10px;
			}
			&:before {
				display: none;
			}
		}
		&:nth-child(2) {
			@include media('>=large-desktop') {
				width: 34.5%;
			}
		}
	}
	.social-list {
		display: flex;
		flex-wrap: wrap;
		@include media('>=large-desktop') {
			margin-top: 10px;
		}
		li {
			margin: 0 10px 10px 0;
			@include media('>=large-desktop') {
				margin-right: 15px;
			}
		}
	}
	p {
		margin-bottom: 5px;
		@include media('>=large-desktop') {
			margin-bottom: 0;
		}
	}
	address {
		font-style: normal;
		font-weight: $font-weight-bold;
		@include media('>=large-desktop') {
			font-size: 18px;
			line-height: 24px;
		}
		p {
			margin-bottom: 0;
		}
	}
}

.sign-up-event {
	background: $color-gray-dark;
	color: $color-white;
	padding: 20px;
	position: relative;
	@include media('>=tablet-landscape') {
		display: flex;
		justify-content: space-between;
		align-items: flex-end;
		padding: 30px;
	}
	@include media('>=large-desktop') {
		padding: 42px 103px 35px 30px;
	}
	h4, .h4 {
		color: $color-white;
	}
	p {
		margin-bottom: 10px;
		@include media('>=tablet-landscape') {
			margin-bottom: 0;
		}
	}
	.button {
		flex-shrink: 0;
		@include media('>=desktop') {
			margin: 0 0 6px 20px;
		}
	}
}

.audio-player{
	margin:  30px 0;
	@include media('>=desktop') {
		margin: 50px 0;
	}
	@include media('>=large-desktop') {
		margin: 100px 0;
	}
}