.footer {
	padding: 50px 0;
	background-color: $color-white;
	@include media('>=desktop') {
		padding: 100px 0;
	}
	@include media('>=large-desktop') {
		padding: 140px 0 118px;
	}

	.logo-holder {
		display: flex;
		justify-content: space-between;
		margin-bottom: 25px;
		@include media('>=desktop') {
			margin-bottom: 40px;
		}
		@include media('>=large-desktop') {
			margin-bottom: 45px;
		}
	}

	.logo {
		max-width: 141px;
		@include media('>=tablet') {
			max-width: 175px;
		}
		@include media('>=large-desktop') {
			max-width: 278px;
		}
	}

	.back-to-top {
		border: 0;
		background-color: transparent;
		padding: 0;
		cursor: pointer;
		font-size: 14px;
		line-height: 20px;
		font-weight: 600;
		@include media('>=desktop') {
			font-size: 16px;
			line-height: 24px;
		}
		@include media('>=large-desktop') {
			font-size: 18px;
			line-height: 24px;
		}

		.no-touch & {
			transition: color .5s ease;

			&:hover {
				color: $color-red-dark;
				text-decoration: underline;
			}
		}
	}

	.copy-holder {
		font-size: 14px;
		line-height: 23px;
		text-align: center;
		color: $color-gray;
		padding-top: 20px;
		@include media('>=tablet-landscape') {
			display: flex;
			justify-content: space-between;
		}
		@include media('>=desktop') {
			font-size: 15px;
			line-height: 24px;
		}
		@include media('>=large-desktop') {
			padding-top: 25px;
		}

		p {
			margin-bottom: 15px;
			@include media('>=tablet-landscape') {
				margin-bottom: 0;
			}
		}
	}

	.add-links {
		display: flex;
		justify-content: center;
		flex-wrap: wrap;
		margin: 0 -7px -15px -8px;
		@include media('>=tablet') {
			margin: 0 0 -20px 0;
		}

		li {
			margin: 0 7px 15px 8px;
			@include media('>=tablet') {
				margin: 0 0 20px 20px;
			}
			@include media('>=large-desktop') {
				margin-left: 33px;
			}
		}

		a {
			.no-touch & {
				&:hover {
					text-decoration: underline;
				}
			}
		}
	}
}

.footer-nav-holder {
	border: solid #D7D7D7;
	border-width: 1px 0;
	padding: 20px 0;
	display: flex;
	justify-content: space-between;
	align-items: flex-start;
	font-size: 16px;
	line-height: 24px;
	letter-spacing: 1px;
	@include media('>=tablet') {
		align-items: center;
	}
	@include media('>=desktop') {
		padding: 50px 0;
		font-size: 18px;
		line-height: 24px;
	}
	@include media('>=large-desktop') {
		font-size: 26px;
		line-height: 34px;
	}

	.footer-nav {
		@include media('>=tablet') {
			display: flex;
			flex-wrap: wrap;
			align-items: center;
			padding-right: 20px;
		}

		li {
			@include media('>=tablet') {
				margin: 0 15px 0 0;
			}
			@include media('>=desktop') {
				margin-right: 25px;
			}
			@include media('>=large-desktop') {
				margin-right: 50px;
			}

			& + li {
				@include media('<tablet') {
					margin-top: 15px;
				}
			}
		}

		a {
			.no-touch & {
				&:hover {
					text-decoration: underline;
				}
			}
		}
	}

	.social {
		display: flex;
		flex-direction: column;
		flex-shrink: 0;
		align-items: flex-end;
		@include media('>=tablet') {
			flex-direction: row;
			align-items: center;
			flex-shrink: 0;
		}

		.title {
			@include media('>=large-desktop') {
				padding-right: 5px;
			}
		}
	}
}

.social-list {
	@include media('>=tablet') {
		display: flex;
		align-items: center;
	}

	li {
		margin-top: 10px;
		@include media('>=tablet') {
			margin: 0 0 0 10px;
		}
		@include media('>=large-desktop') {
			margin-left: 15px;
		}
	}

	a {
		display: block;
		width: 40px;
		height: 40px;
		border: 1px solid $color-red-dark;
		position: relative;

		.no-touch & {
			transition: background-color .5s ease;

			&:hover {
				background-color: $color-red-dark;

				svg path {
					fill: $color-white;
				}
			}
		}
	}

	svg {
		position: absolute;
		left: 50%;
		top: 50%;
		transform: translate(-50%, -50%);
		max-width: 16px;
		max-height: 16px;

		path {
			transition: fill .5s ease;
		}
	}
}

.recognized-in {
	background-color: $color-gray-light;
	padding: 50px 0;
	@include media('>=desktop') {
		padding: 70px 0;
	}

	h6, .h6 {
		text-align: center;
		text-transform: uppercase;
		margin-bottom: 20px;
		@include media('>=desktop') {
			margin-bottom: 30px;
		}
		@include media('>=large-desktop') {
			margin-bottom: 50px;
		}
	}

	.logos-list {
		display: flex;
		align-items: center;
		flex-wrap: wrap;
		justify-content: center;
		margin: 0 -20px -20px;
		@include media('>=tablet') {
			margin: 0 0 -20px;
			justify-content: space-between;
		}
	}

	.item {
		margin: 0 15px 20px;
		@include media('>=desktop') {
			margin: 0 20px 20px;
			width: 250px;
			text-align: center;
		}

		&:first-child {
			text-align: left;
		}
	}

	img {
		max-width: 100px;
		max-height: 69px;
		@include media('>=tablet') {
			max-width: 165px;
			max-height: 100px;
		}
		@include media('>=desktop') {
			max-width: 250px;
			max-height: 139px;
		}
	}
}