.info-boxes {
	padding: 100px 0 90px;
	background-color: $color-red-dark;
	color: $color-white;
	margin-bottom: 125px;
	@include media('>=desktop') {
		margin-bottom: 130px;
		padding: 140px 0 150px;
	}
	@include media('>=large-desktop') {
		padding: 196px 0 123px;
		margin-bottom: 180px;
	}

	.holder {
		@include media('>=tablet') {
			margin-left: -30px;
			display: flex;
			align-items: flex-start;
		}
		@include media('>=large-desktop') {
			margin-left: -90px;
		}
	}

	.item {
		display: flex;
		@include media('>=tablet') {
			width: calc(50% - 30px);
			margin-left: 30px;
		}
		@include media('>=large-desktop') {
			width: calc(50% - 90px);
			margin-left: 90px;
		}

		& + .item {
			@include media('<tablet') {
				margin-top: 50px;
			}
		}
	}

	.photo {
		height: 83px;
		width: 80px;
		margin-right: 10px;
		flex-shrink: 0;
		background: {
			size: cover;
			position: 50% 50%;
			repeat: no-repeat;
		}
		@include media('>=desktop') {
			width: 172px;
			height: 178px;
			margin-right: 30px;
		}
		@include media('>=large-desktop') {
			width: 278px;
			height: 287px;
		}
	}

	h3, .h3 {
		color: $color-white;
		@include media('>=large-desktop') {
			margin-bottom: 13px;
		}
	}

	.button {
		margin-top: 30px;
		color: $color-red-dark;
		@include media('>=large-desktop') {
			margin-top: 37px;
		}
	}
}