.add-page-menu {
	margin-bottom: 30px;
	@include media('>=desktop') {
		margin-bottom: 75px;
		font-size: 14px;
	}
	@include media('>=large-desktop') {
		margin-bottom: 100px;
		font-size: 18px;
	}
	nav {
		padding: 10px;
		background-color: $color-gray-light;
		border: solid rgba($color-gray, .5);
		border-width: 1px 0;
		text-align: center;
		font-weight: $font-weight-medium;
		@include media('>=tablet') {
			padding: 0;
		}
	}

	ul {
		@include media('>=tablet') {
			display: flex;
			flex-wrap: wrap;
			margin: 0 -20px;
		}
		@include media('>=desktop') {
			padding-left: 390px;
			margin: 0 -10px;
		}
		@include media('>=large-desktop') {
			margin: 0 -35px;
		}
	}

	li {
		position: relative;
		@include media('>=tablet') {
			padding: 20px 0;
			margin: 0 20px;
			display: flex;
		}
		@include media('>=desktop') {
			margin: 0 10px;
		}
		@include media('>=large-desktop') {
			padding: 40px 0;
			margin: 0 35px;
		}
	}

	a {
		.no-touch & {
			transition: color .5s ease;

			&:hover {
				color: $color-red-dark;
				&:after {
					opacity: 1;
				}
			}
		}

		&:after {
			content: "";
			position: absolute;
			bottom: -1px;
			left: 50%;
			width: 100%;
			height: 3px;
			background-color: $color-black;
			transform: translateX(-50%);
			pointer-events: none;
			opacity: 0;
			transition: opacity .5s;
		}
	}
}