.article-holder {
	@include media('>=large-desktop') {
		margin-bottom: 150px;
	}
}

.article-content {
	@include media('>=tablet-landscape') {
		width: 70%;
	}
	hr {
		margin: 30px 0;
		@include media('>=large-desktop') {
			margin: 50px 0;
		}
	}
	p {
		margin-bottom: 15px;
		&.large-text {
			@include media('>=large-desktop') {
				font-size: 24px;
				line-height: 41px;
			}
		}
	}
	h4, .h4 {
		padding-top: 0;
		@include media('>=large-desktop') {
			margin-bottom: 20px;
		}
	}
	h5, .h5 {
		font-weight: $font-weight-roman;
	}
	.video-holder {
		border-radius: 10px;
		box-shadow: 0 16px 32px rgba(35, 23, 5, 0.26);
		@include media('>=desktop') {
			margin-bottom: 30px;
		}
		.poster {
			border-radius: 10px;
		}
		iframe {
			border-radius: 10px;
		}
		+ h5, .h5 {
			padding-top: 0;
			font-weight: $font-weight-roman;
			@include media('>=large-desktop') {
				margin-bottom: 50px;
			}
		}
	}
	.quote-holder {
		display: flex;
		align-items: flex-start;
		padding: 20px;
		margin: 30px 0;
		background: $color-gray-dark;
		@include media('>=tablet') {
			padding: 50px;
		}
		@include media('>=desktop') {
			flex-shrink: 0;
			margin: 50px 0;
		}
		@include media('>=large-desktop') {
			padding: 150px 115px 136px 80px;
			margin: 100px 0;
		}
		.quote {
			border-left: 1px solid $color-white;
			padding-left: 15px;
			color: $color-white;
			@include media('>=desktop') {
				padding-left: 33px;
			}
		}
		.title {
			margin-bottom: 20px;
			@include media('>=desktop') {
				font-size: 16px;
				line-height: 24px;
			}
			@include media('>=large-desktop') {
				margin-bottom: 30px;
			}
		}
		blockquote {
			font-style: italic;
			@include media('>=desktop') {
				font-size: 18px;
				line-height: 30px;
			}
			@include media('>=large-desktop') {
				font-size: 24px;
				line-height: 41px;
			}
			p {
				margin: 0;
			}
		}
		h4, .h4 {
			color: $color-white;
			@include media('>=desktop') {
				margin-bottom: 15px;
			}
		}
		svg {
			width: 39px;
			height: auto;
			display: block;
			stroke-width: 2px;
			stroke: $color-white;
			flex-shrink: 0;
			margin: 22px 20px 0 10px;
			@include media('>=desktop') {
				width: 78px;
				margin-right: 35px;
			}
			@include media('>=large-desktop') {
				width: 130px;
				margin-right: 56px;
				height: 136px;
			}
		}
	}
	p {
		a {
			color: $color-red;
			text-decoration: underline;
			.no-touch &:hover {
				text-decoration: none;
			}
		}
	}
}

.speakers {
	margin-bottom: 40px;
	@include media('>=large-desktop') {
		max-width: 905px;
		margin-top: 98px;
		margin-bottom: 50px;
	}
	h4, .h4 {
		margin-bottom: 20px;
		@include media('>=large-desktop') {
			margin-bottom: 45px;
		}
	}
	.team-list.columns {
		@include media('>=tablet') {
			display: flex;
			margin: 0 -10px;
		}
		.item {
			@include media('>=tablet') {
				padding: 0 10px 20px;
				margin: 0;
				width: 50%;
			}
			@include media('>=large-desktop') {
				padding-bottom: 35px;
			}
		}
		.photo-holder {
			@include media('>=tablet') {
				width: 134px;
				height: 118px;
				padding: 0;
			}
		}
	}
	.social-list {
		display: flex;
		margin-top: 10px;
		li {
			margin: 0 10px 10px 0;
		}
	}
	ul {
		margin-bottom: 0 !important;
		li {
			padding-left: 0;
			margin-bottom: 0;
			&:before {
				display: none;
			}
		}
	}
}

.agenda {
	h4, .h4 {
		margin-bottom: 20px;
		@include media('>=large-desktop') {
			margin-bottom: 50px;
		}
	}
	.tabs-nav {
		display: flex;
		font-size: 16px;
		margin-bottom: 10px;
		@include media('>=large-desktop') {
			font-size: 26px;
			line-height: 34px;
			letter-spacing: 1px;
		}
		li {
			margin-bottom: 6px;
			&:before {
				content: '';
				display: inline-block;
				vertical-align: middle;
				margin: 0 10px;
				width: 1px;
				height: 16px;
				background: $color-gray;
				@include media('>=large-desktop') {
					margin: 0 70px;
				}
			}
			&:first-child {
				&:before {
					display: none;
				}
			}
			&.active {
				a {
					color: $color-red;
					&:after {
						width: 100%;
						opacity: 1;
					}
				}
			}
			a {
				display: inline-block;
				vertical-align: top;
				position: relative;
				color: $color-gray-dark;
				@include media('>=desktop') {
					transition: color .3s;
					padding-left: 10px;
				}
				&:after {
					content: '';
					position: absolute;
					left: 0;
					top: 100%;
					height: 3px;
					background: $color-red-dark;
					margin-top: 5px;
					opacity: 0;
					width: 0;
					transition: opacity .3s, width .3s;
					@include media('>=tablet') {
						margin-top: 15px;
					}
				}
				.no-touch &:hover {
					color: $color-red;
				}
			}
		}
	}
	.accordion {
		h5, .h5 {
			@include media('>=tablet') {
				display: flex;
			}
			@include media('>=large-desktop') {
				font-size: 18px;
				line-height: 24px;
			}
			> a {
				font-weight: $font-weight-bold;
				@include media('>=tablet') {
					flex-grow: 1;
				}
			}
		}
	}
	.accordion-item {
		@include media('>=desktop') {
			padding: 9px 0;
		}
	}
	.date {
		display: block;
		padding-top: 10px;
		color: $color-gray;
		@include media('>=tablet') {
			width: 29.5%;
			flex-shrink: 0;
			padding-right: 15px;
		}
		@include media('>=desktop') {
			padding-top: 7px;
		}
		@include media('>=large-desktop') {
			font-size: 18px;
			line-height: 33px;
			letter-spacing: .5px;
		}
	}
	.accordion-info {
		@include media('>=tablet') {
			padding-left: 29.5%;
			padding-right: 40px;
		}
		p {
			@include media('>=large-desktop') {
				margin-bottom: 10px;
			}
		}
	}
}

.images-slider-wrap {
	position: relative;
	width: 100%;
	margin-bottom: 30px;
	@include media('>=desktop') {
		margin-bottom: 50px;
	}
	@include media('>=large-desktop') {
		margin-bottom: 100px;
	}
	.image {
		background: {
			repeat: no-repeat;
			position: 50% 50%;
			size: cover;
		}
		height: 250px;
		@include media('>=tablet') {
			height: 400px;
		}
		@include media('>=desktop') {
			padding-bottom: 55.7%;
		}
	}
	figcaption {
		text-align: center;
		color: $color-gray;
		margin-top: 20px;
		@include media('>=large-desktop') {
			font-size: 24px;
			line-height: 41px;
			margin-top: 30px;
		}
	}
	.images-slider-nav {
		position: relative;
		z-index: 20;
		display: flex;
		align-items: center;
		justify-content: center;
		padding: 0 15px;

		.slick-arrow {
			&.slick-prev {
				margin-right: 20px;
			}

			&.slick-next {
				order: 10;
				margin-left: 20px;
			}

			svg {
				vertical-align: top;
			}
		}

		.slick-dots {
			margin: 0 0 0 -15px;
			display: flex;
			align-items: center;

			li {
				margin: 0 0 0 15px;
				font-size: 0;
				line-height: 0;
				padding: 0;
				&:before {
					display: none;
				}

				&.slick-active {
					button {
						background-color: $color-black;
					}
				}
			}

			button {
				text-indent: -9999px;
				overflow: hidden;
				position: relative;
				width: 10px;
				height: 10px;
				border: 1px solid $color-black;
				transition: background-color .5s ease;
				border-radius: 50%;
			}
		}
	}
	.slick-btn {
		position: absolute;
		top: 30%;
		width: 50px;
		height: 50px;
		background-color: $color-gray-dark;
		display: flex;
		align-items: center;
		justify-content: center;
		margin-bottom: 20px;
		transition: background-color .5s ease;
		z-index: 40;
		@include media('>=tablet') {
			top: 35%;
		}
		@include media('>=large-desktop') {
			top: 38%;
		}
		.no-touch & {
			&:hover {
				background-color: $color-red-dark;
			}
		}

		&.button-prev {
			left: 0;
		}

		&.button-next {
			right: 0;
		}

		svg {
			width: 13px;
			height: auto;
		}
	}
}

.images-slider {
	margin-bottom: 23px;
}

.article-aside {
	.speakers {
		margin: 0;
		@include media('>=large-desktop') {
			margin-bottom: -25px;
		}
		h4, .h4 {
			@include media('>=large-desktop') {
				margin-bottom: 25px;
			}
		}
		.team-list {
			padding: 0;
			@include media('>=tablet-landscape') {
				margin: 0;
			}
			@include media('>=large-desktop') {
				margin-left: 10px;
			}
			.item {
				@include media('>=mobile-landscape') {
					width: 50%;
				}
				@include media('>=tablet-landscape') {
					display: block;
					width: 100%;
					margin: 0;
				}
				@include media('>=large-desktop') {
					padding-bottom: 20px;
				}
			}
			.photo-holder {
				@include media('>=mobile-landscape') {
					width: 134px !important;
					height: 118px;
				}
			}
		}
	}
}
