.error-page {
	margin-top: -100px;
	min-height: 100vh;
	padding: 70px 0 50px;
	position: relative;
	z-index: 20;
	display: flex;
	align-items: flex-end;
	color: $color-white;
	background: {
		size: cover;
		position: 50% 50%;
		repeat: no-repeat;
	}
	@include media('>=tablet') {
	    min-height: 500px;
	}
	@include media('>=desktop') {
		padding: 200px 0 70px;
		margin-top: -150px;
		min-height: 600px;
	}
	@include media('>=large-desktop') {
		margin-top: -300px;
		padding-top: 350px;
		min-height: 700px;
	}

	&:after {
		content: "";
		position: absolute;
		left: 0;
		top: 0;
		width: 100%;
		height: 100%;
		background: linear-gradient(0deg, $color-gray-dark, $color-gray-dark);
		opacity: 0.6;
		z-index: -1;
	}

	.wrap {
		max-width: 895px;
		font-size: 18px;
		line-height: 24px;
		@include media('>=desktop') {
			font-size: 20px;
			line-height: 34px;
		}
		@include media('>=large-desktop') {
			font-size: 24px;
			line-height: 41px;
		}
	}

	h1, .h1 {
		color: $color-white;
		margin-bottom: 10px;
	}

	.button {
		margin-top: 30px;
		@include media('>=large-desktop') {
			margin-top: 50px;
		}
	}
}