.plans {
	margin-bottom: 50px;
	@include media('>=desktop') {
		margin-bottom: 70px;
	}
	@include media('>=large-desktop') {
		margin-bottom: 100px;
	}
	.holder {
		@include media('>=tablet-landscape') {
			display: flex;
		}
	}
	h2, .h2 {
		margin-bottom: 10px;
		@include media('>=tablet-landscape') {
			width: 30%;
			flex-shrink: 0;
			margin: 10px 40px 0 0;
		}
		@include media('>=large-desktop') {
			margin: 35px 160px 0 0;
		}
	}
}

.marked-list {
	overflow: hidden;
	@include media('>=large-desktop') {
		font-size: 26px;
		line-height: 34px;
		letter-spacing: 1px;
	}
	ul {
		margin-bottom: -2px;
		@include media('>=tablet') {
			display: flex;
			flex-wrap: wrap;
			margin: 0 -10px -2px;
		}
		@include media('>=large-desktop') {
			margin-left: -32px;
			margin-right: -7px;
		}
	}
	li {
		position: relative;
		padding: 20px 0 20px 30px;
		border-bottom: 1px solid rgba($color-gray, .5);
		@include media('>=tablet') {
			width: calc(50% - 20px);
			margin: 0 10px;
		}
		@include media('>=large-desktop') {
			width: calc(50% - 64px);
			padding: 45px 25px 45px 60px;
			margin: 0 32px;
		}
		&:before {
			content: '';
			position: absolute;
			left: 0;
			top: 50%;
			width: 20px;
			height: 20px;
			margin-top: -10px;
			background: url("#{$images-path}checkbox-icon.svg") no-repeat;
			background-size: 100% 100%;
			@include media('>=large-desktop') {
				width: 30px;
				height: 30px;
				top: 0;
				margin-top: 45px;
			}
		}
	}
}