.news-alert {
	width: 100%;
	opacity: 0;
	visibility: hidden;
	position: absolute;
	transition: opacity .5s ease, visibility .5s ease;

	&.show {
		position: relative;
		opacity: 1;
		visibility: visible;
	}
}

.news-alert-holder {
	padding: 20px 15px;
	background-color: $color-white;
	position: relative;
	@include media('>=tablet-landscape') {
		padding: 20px 202px 20px 0;
	}
	@include media('>=desktop') {
		padding: 30px 202px 30px 0;
		margin: 0 auto;
		max-width: 980px;
	}
	@include media('>=medium-desktop') {
		max-width: 1200px;
	}
	@include media('>=large-desktop') {
		padding: 40px 370px 40px 0;
		min-height: 147px;
		max-width: 1510px;
	}

	.subtitle {
		color: #B60000;
		text-transform: capitalize;
		font-weight: $font-weight-medium;
		font-size: 14px;
		line-height: 20px;
		margin-bottom: 14px;
		padding-right: 60px;
		@include media('>=tablet-landscape') {
			position: absolute;
			top: 2px;
			left: 15px;
			font-size: 16px;
			line-height: 20px;
			padding-right: 0;
			width: 175px;
			white-space: nowrap;
			text-overflow: ellipsis;
			overflow: hidden;
		}
		@include media('>=desktop') {
			left: 43px;
			width: 150px;
		}
		@include media('>=large-desktop') {
			left: 84px;
			font-size: 18px;
			line-height: 20px;
			width: 250px;
		}
	}

	.clear {
		position: absolute;
		top: 20px;
		right: 35px;
		color: $color-gray;
		font-size: 14px;
		line-height: 18px;
		@include media('>=tablet-landscape') {
			top: 50%;
			transform: translateY(-50%);
		}
		@include media('>=desktop') {
			font-size: 15px;
			line-height: 24px;
			right: 60px;
		}
		@include media('>=large-desktop') {
			font-size: 18px;
			line-height: 24px;
			right: 84px;
		}

		span {
			&:after {
				background-color: $color-gray;
			}
		}

		&:before, &:after {
			background-color: $color-gray;
		}
	}

	.learn-more {
		color: $color-gray;

		span {
			&:after {
				background-color: $color-gray;
			}
		}
	}

	.news-alert-slider-nav {
		@include media('>=tablet-landscape') {
			position: absolute;
			top: 40px;
			left: 15px;
		}
		@include media('>=desktop') {
			top: 54px;
			left: 43px;
		}
		@include media('>=large-desktop') {
			top: 74px;
			left: 84px;
		}
	}
}

.news-alert-slider {
	.item{
		@include media('>=tablet-landscape') {
			padding-left: 202px;
		}
		@include media('>=large-desktop') {
			padding-left: 370px;
		}
	}
}

.news-alert-slider-nav {
	color: $color-gray;
	display: flex;
	align-items: center;
	justify-content: center;
	padding-top: 15px;

	.nav-count {
		padding: 0 19px;
		width: 70px;
		text-align: center;
	}

	i {
		font-style: normal;
	}
}