$fonts-path: '../fonts/';

@font-face {
	font-family: 'Neue Haas Grotesk Display Pro';
	src: local('NeueHaasDisplay-Roman'),
	url('#{$fonts-path}NeueHaasDisplay-Roman.woff2') format('woff2'),
	url('#{$fonts-path}NeueHaasDisplay-Roman.woff') format('woff');
	font-weight: 500;
	font-style: normal;
}

@font-face {
	font-family: 'Neue Haas Grotesk Display Pro';
	src: local('NeueHaasDisplay-Medium'),
	url('#{$fonts-path}NeueHaasDisplay-Medium.woff2') format('woff2'),
	url('#{$fonts-path}NeueHaasDisplay-Medium.woff') format('woff');
	font-weight: normal;
	font-style: normal;
}

@font-face {
	font-family: 'Neue Haas Grotesk Display Pro';
	src: local('NeueHaasDisplay-Bold'),
	url('#{$fonts-path}NeueHaasDisplay-Bold.woff2') format('woff2'),
	url('#{$fonts-path}NeueHaasDisplay-Bold.woff') format('woff');
	font-weight: bold;
	font-style: normal;
}