.capabilities {
	margin-bottom: 50px;
	@include media('>=desktop') {
		margin-bottom: 100px;
	}
	@include media('>=large-desktop') {
		margin-bottom: 150px;
	}
	.heading {
		max-width: 895px;
		margin: 0 auto 30px;
		text-align: center;
		@include media('>=large-desktop') {
			margin-bottom: 63px;
		}
	}
	.tabs-nav {
		text-align: center;
		margin-bottom: 40px;
		@include media('>=tablet') {
			display: flex;
			justify-content: center;
			border-bottom: 1px solid rgba($color-gray, .5);
			padding-bottom: 16px;
			font-size: 16px;
		}
		@include media('>=desktop') {
			margin-bottom: 60px;
		}
		@include media('>=large-desktop') {
			font-size: 26px;
			line-height: 34px;
			letter-spacing: 1px;
			margin-bottom: 100px;
		}
		li {
			margin-bottom: 15px;
			@include media('>=tablet') {
				margin: 0;
			}
			&:before {
				@include media('>=tablet') {
					content: '';
					display: inline-block;
					vertical-align: middle;
					margin: 0 10px;
					width: 1px;
					height: 16px;
					background: $color-gray;
				}
				@include media('>=large-desktop') {
					margin: 0 70px;
				}
			}
			&:first-child {
				&:before {
					display: none;
				}
			}
			&.active {
				a {
					color: $color-red;
					&:after {
						width: 100%;
						opacity: 1;
					}
				}
			}
			a {
				display: inline-block;
				vertical-align: top;
				position: relative;
				color: $color-gray-dark;
				@include media('>=desktop') {
					transition: color .3s;
				}
				&:after {
					content: '';
					position: absolute;
					left: 0;
					top: 100%;
					height: 3px;
					background: $color-red-dark;
					margin-top: 5px;
					opacity: 0;
					width: 0;
					transition: opacity .3s, width .3s;
					@include media('>=tablet') {
						margin-top: 15px;
					}
				}
				.no-touch &:hover {
					color: $color-red;
				}
			}
		}
	}
	.tabs-holder {
		@include media('>=large-desktop') {
			margin: 0 153px;
		}
	}
}

.capabilities-list {
	text-align: center;
	justify-content: center;
	display: flex;
	flex-wrap: wrap;
	margin: 0 -10px;
	li {
		width: 50%;
		padding: 0 10px 20px;
		@include media('>=tablet') {
			width: 33.33%;
		}
		@include media('>=tablet-landscape') {
			width: 25%;
		}
		@include media('>=desktop') {
			padding-bottom: 40px;
		}
	}
	.icon {
		width: 60px;
		height: 60px;
		margin: 0 auto 20px;
		@include media('>=large-desktop') {
			margin-bottom: 30px;
		}
	}
	a{
		transition: color .5s;
		.no-touch &:hover{
			color: $color-red;
		}
	}
}