.clients-testimonials {
	padding: 40px 0;
	margin-bottom: 90px;
	position: relative;
	color: $color-white;
	@include media('>=desktop') {
		margin-bottom: 120px;
		padding: 50px 0;
	}
	@include media('>=large-desktop') {
		padding: 100px 0 50px;
		margin-bottom: 150px;
	}

	&:after {
		content: "";
		position: absolute;
		top: 0;
		left: 50%;
		bottom: 0;
		transform: translateX(-50%);
		width: 105vw;
		z-index: -1;
		background-color: $color-gray-dark;
	}

	h2, .h2 {
		color: $color-white;
		margin-bottom: 30px;
		@include media('>=desktop') {
			margin-bottom: 50px;
		}
		@include media('>=large-desktop') {
			margin-bottom: 70px;
		}
	}

	.slider-holder {
		position: relative;
	}

	.testimonials-slider-nav {
		position: absolute;
		top: 251px;
		padding: 0;
		@include media('>=mobile-landscape') {
			top: 340px;
		}
		@include media('>=tablet') {
			top: 270px;
			transform: translateX(-100%);
			left: 295px;
		}
		@include media('>=desktop') {
			top: 300px;
			left: 345px;
		}
		@include media('>=large-desktop') {
			padding: 0;
			top: 305px;
			left: 445px;
		}
	}
}

.clients-testimonials-slider {
	position: relative;
	z-index: 20;
	color: $color-white;
	padding-bottom: 30px;
	@include media('>=desktop') {
		padding-bottom: 50px;
	}

	.holder {
		@include media('>=tablet') {
			display: flex;
		}
	}

	.photo-wrap {
		flex-shrink: 0;
		display: flex;
		align-items: flex-start;
		padding-bottom: 50px;
		@include media('>=tablet') {
			width: 350px;
			padding: 0 50px 50px 0;
		}
		@include media('>=desktop') {
			padding-bottom: 65px;
			width: 400px;
		}
		@include media('>=large-desktop') {
			width: 547px;
			padding: 0 100px 61px 0;
		}
	}

	svg {
		width: 39px;
		height: auto;
		display: block;
		stroke-width: 2px;
		stroke: $color-white;
		flex-shrink: 0;
		margin-right: 20px;
		@include media('>=desktop') {
			width: 78px;
			margin-right: 30px;
		}
		@include media('>=large-desktop') {
			width: 120px;
			margin-right: 30px;
		}
	}

	.photo {
		height: 231px;
		flex-grow: 1;
		background: {
			size: cover;
			position: 50% 50%;
			repeat: no-repeat;
		}
		@include media('>=mobile-landscape') {
			height: 320px;
		}
		@include media('>=tablet') {
			height: 250px;
		}
		@include media('>=large-desktop') {
			padding-top: 0;
			width: 278px;
			height: 260px;
		}
	}

	.info {
		@include media('>=tablet') {
			border-left: 1px solid $color-white;
			padding-left: 50px;
		}
		@include media('>=large-desktop') {
			padding-left: 89px;
		}
	}

	h3, .h3 {
		color: $color-white;
		@include media('>=desktop') {
			margin-bottom: 15px;
		}
	}

	.heading {
		display: flex;
		align-items: center;
		padding-bottom: 20px;
		@include media('>=desktop') {
			padding-bottom: 30px;
		}

		p {
			font-size: 14px;
			line-height: 14px;
			letter-spacing: 0.5px;
			text-transform: uppercase;
			margin-bottom: 5px;
			@include media('>=desktop') {
				font-size: 16px;
				line-height: 16px;
				margin-bottom: 15px;
			}
		}
	}

	.logo {
		width: 71px;
		flex-shrink: 0;
		margin-right: 20px;
		@include media('>=desktop') {
			margin-right: 30px;
		}
	}

	h3, .h3 {
		color: $color-white;
		margin-bottom: 0;
	}

	blockquote {
		font-style: italic;
		@include media('>=desktop') {
			font-size: 18px;
			line-height: 30px;
		}
		@include media('>=large-desktop') {
			font-size: 24px;
			line-height: 41px;
		}
	}
}