.leadership {
	margin-bottom: 50px;
	@include media('>=desktop') {
		margin-bottom: 70px;
	}
	@include media('>=large-desktop') {
		margin-bottom: 100px;
	}

	h2, .h2 {
		text-align: center;
		margin-bottom: 30px;
		@include media('>=desktop') {
			margin-bottom: 50px;
		}
		@include media('>=large-desktop') {
			margin-bottom: 85px;
		}
	}

	h3, .h3 {
		margin-bottom: 30px;
		@include media('>=desktop') {
			margin-bottom: 50px;
		}
	}
}

.leadership-details {
	margin-bottom: 50px;
	@include media('>=tablet') {
		display: flex;
	}
	@include media('>=desktop') {
		margin-bottom: 80px;
	}
	@include media('>=large-desktop') {
		margin-bottom: 117px;
	}

	.box {
		border: 1px solid rgba(0, 0, 0, 0.12);
		padding: 20px;
		@include media('>=tablet') {
			width: 50%;
			padding: 50px;
		}
		@include media('>=large-desktop') {
			padding: 70px 100px 65px 70px;
		}

		p {
			color: $color-gray;
			margin-bottom: 20px;
			@include media('>=desktop') {
				font-size: 20px;
				line-height: 30px;
			}
			@include media('>=large-desktop') {
				font-size: 24px;
				line-height: 41px;
				margin-bottom: 38px;
			}
		}
	}

	.person-contacts {
		@include media('>=large-desktop') {
			margin-top: 30px;
		}
	}
}

.person-contacts {
	h4, .h4 {
		margin-bottom: 6px;
	}

	a {
		@include media('>=desktop') {
			transition: color .3s;
		}

		.no-touch &:hover {
			color: $color-red;
		}
	}
}

.person-post {
	text-transform: uppercase;
	color: $color-gray;
	margin-bottom: 8px;
}

.team-list {
	@include media('>=mobile-landscape') {
		margin: 0 -10px;
		display: flex;
		flex-wrap: wrap;
	}
	@include media('>=large-desktop') {
		margin: 0 -15px;
	}

	.item {
		position: relative;
		z-index: 20;
		max-width: 400px;
		width: 100%;
		margin: 0 auto 30px;
		@include media('>=mobile-landscape') {
			max-width: calc(50% - 20px);
			margin: 0 10px 20px;
		}
		@include media('>=tablet-landscape') {
			max-width: calc(33.333% - 20px);
		}
		@include media('>=desktop') {
			max-width: calc(25% - 20px);
		}
		@include media('>=large-desktop') {
			max-width: calc(25% - 30px);
			margin: 0 15px 30px;
		}
		img{
			position: absolute;
			opacity: 0;
		}
	}

	.photo-holder {
		position: relative;
		overflow: hidden;
		z-index: 20;
		margin-bottom: 10px;
		padding-bottom: 100%;
		@include media('>=desktop') {
			width: 230px;
			height: 230px;
			padding: 0;
		}
		@include media('>=large-desktop') {
			width: 355px;
			height: 360px;
		}

		.no-touch &:hover {
			.photo {
				transform: translate(-50%, -50%) scale(1.05);
			}
		}
	}

	.photo {
		position: absolute;
		top: 50%;
		left: 50%;
		transform: translate(-50%, -50%);
		width: 100%;
		height: 100%;
		transition: transform .5s ease;
		background: {
			size: cover;
			position: 50% 50%;
			repeat: no-repeat;
		}

		> a {
			position: absolute;
			left: 0;
			top: 0;
			width: 100%;
			height: 100%;
			color: transparent;
			z-index: 50;
		}
	}

	h5, .h5 {
		margin-bottom: 3px;

		a {
			transition: color .3s;

			.no-touch &:hover {
				color: $color-red;
			}
		}
	}

	h4, .h4, h6, .h6 {
		@include media('>=desktop') {
			margin-bottom: 6px;
		}

		a {
			transition: color .3s;

			.no-touch &:hover {
				color: $color-red;
			}
		}
	}

	p {
		a {
			.no-touch & {
				&:hover {
					text-decoration: underline;
				}
			}
		}
	}

	&.columns {
		@include media('>=mobile-landscape') {
			display: block;
			flex: none;
			margin: 0;
		}
		@include media('>=tablet-landscape') {
			display: flex;
			margin: 0 -10px;
		}
		@include media('>=desktop') {
			margin: 0 -15px;
		}

		.item {
			max-width: none;
			margin: 0 0 20px;
			@include media('>=mobile-landscape') {
				display: flex;
			}
			@include media('>=tablet-landscape') {
				width: calc(50% - 20px);
				margin: 0 10px 10px;
			}
			@include media('>=desktop') {
				width: calc(50% - 30px);
				margin: 0 15px 40px;
			}
		}

		.photo-holder {
			@include media('>=mobile-landscape') {
				width: 40%;
				flex-shrink: 0;
				padding-bottom: 35%;
				margin-right: 20px;
				height: auto;
			}
			@include media('>=desktop') {
				width: 276px;
				height: 244px;
				padding-bottom: 0;
			}
		}
	}

	&.three-in-row {
		.item {
			@include media('>=desktop') {
				max-width: calc(33.333% - 20px);
				margin-bottom: 30px;
			}
			@include media('>=large-desktop') {
				max-width: calc(33.333% - 30px);
				margin-bottom: 50px;
			}
		}

		.photo-holder {
			@include media('>=desktop') {
				width: auto;
				height: auto;
				padding-top: 87.6%;
				margin-bottom: 20px;
			}
		}

		.person-post {
			@include media('>=desktop') {
				margin-bottom: 15px;
			}
		}
	}

	&.two-in-row {
		@include media('>=large-desktop') {
			margin-bottom: 115px;
		}

		.item {
			@include media('>=tablet-landscape') {
				max-width: calc(50% - 20px);
				margin-bottom: 30px;
			}
			@include media('>=large-desktop') {
				max-width: calc(50% - 30px);
			}
		}

		.photo-holder {
			@include media('>=desktop') {
				width: auto;
				height: auto;
				padding-top: 87.6%;
				margin-bottom: 20px;
			}
		}
	}
}

.more {
	text-align: center;
}