.filter-for-listing {
	margin-bottom: 40px;
	@include media('>=desktop') {
		margin-bottom: 70px;
	}
	@include media('>=large-desktop') {
		margin-bottom: 100px;
	}

	& + .alphabet-filter {
		margin-top: -10px;
		@include media('>=desktop') {
			margin-top: -20px;
		}
		@include media('>=large-desktop') {
			margin-top: -30px;
		}
	}

	form {
		@include media('>=large-desktop') {
			display: flex;
			justify-content: space-between;
			align-items: flex-end;
			flex-wrap: wrap;
		}
	}

	.select-holder {
		margin-bottom: 20px;
		@include media('>=tablet') {
			display: flex;
			align-items: flex-end;
			margin-left: -20px;
		}
		@include media('>=desktop') {
			margin-left: -30px;
		}
		@include media('>=large-desktop') {
			margin-bottom: 0;
			flex-grow: 1;
		}

		.wrap {
			@include media('>=tablet') {
				flex-grow: 1;
				flex-shrink: 1;
				flex-basis: 0;
				margin-left: 20px;
			}
			@include media('>=desktop') {
				margin-left: 30px;
				max-width: 277px;
				width: 100%;
			}

			& + .wrap {
				@include media('<tablet') {
					margin-top: 20px;
				}
			}

			.button {
				margin: 0;
				@include media('>=tablet') {
					font-size: 15px;
					padding: 12px 20px;
				}
				@include media('>=desktop') {
					margin: 0;
				}
			}
		}
	}

	.keyword-holder {
		@include media('>=large-desktop') {
			flex-shrink: 0;
		}

		.wrap {
			@include media('>=desktop') {
				display: flex;
				align-items: center;
				flex-shrink: 0;
			}
		}
	}

	.add-filters {
		width: 100%;
		padding: 30px 0;
		line-height: 0;

		.add-filters-holder {
			display: none;

		}

		.select-holder {
			padding-top: 20px;
		}
	}

	.actions-wrap {
		display: flex;
		align-items: center;


		.button {
			margin: 0 30px 0 0;
		}
	}

	input[type="text"],
	input[type="password"],
	input[type="email"],
	input[type="tel"],
	input[type="number"],
	textarea {
		@include media('>=large-desktop') {
			width: 277px;
		}
	}

	.button {
		margin-top: 20px;
		@include media('>=desktop') {
			margin: 0 0 0 30px;
		}
	}
}

.alphabet-filter {
	border-top: 1px solid rgba($color-gray, .5);
	padding: 35px 0;
	@include media('>=desktop') {
		padding: 50px 0;
	}
	@include media('>=large-desktop') {
		padding: 70px 0 50px;
	}

	.alphabet-list {
		display: flex;
		flex-wrap: wrap;
		text-transform: uppercase;
		font-size: 18px;
		line-height: 20px;
		margin: 0 0 -15px -15px;
		padding-top: 10px;
		@include media('>=desktop') {
			font-size: 22px;
			line-height: 30px;
			margin: 0 0 -30px -30px;
		}
		@include media('>=large-desktop') {
			font-size: 26px;
			line-height: 34px;
			margin: 0 0 -40px -40px;
			padding-top: 20px;
		}

		li {
			margin: 0 0 15px 15px;
			@include media('>=desktop') {
				margin: 0 0 30px 30px;
			}
			@include media('>=large-desktop') {
				margin: 0 0 40px 40px;
			}
		}

		a {
			transition: color .5s ease;

			&.active {
				color: $color-red-dark;
			}

			.no-touch & {
				&:hover {
					color: $color-red-dark;
				}
			}
		}
	}

	.search-result {
		padding-top: 35px;
		color: $color-gray;
		@include media('>=desktop') {
			padding-top: 50px;
		}
		@include media('>=large-desktop') {
			padding-top: 70px;
		}

		strong {
			font-weight: $font-weight-medium;
			color: $color-black;
		}
	}
}

.add-more {
	display: inline-block;
	vertical-align: top;
	font-weight: $font-weight-roman;
	font-size: 16px;
	line-height: 18px;
	position: relative;
	z-index: 10;
	@include media('>=desktop') {
		font-size: 18px;
		line-height: 18px;
	}

	&.active {
		&:after {
			height: 0;
		}
	}

	.no-touch & {
		&:hover {

			span {
				&:after {
					width: 0;
				}
			}
		}
	}

	&:before {
		content: "";
		position: absolute;
		right: -20px;
		top: 50%;
		transform: translateY(-50%);
		height: 1px;
		width: 10px;
		background-color: $color-black;
	}

	&:after {
		content: "";
		position: absolute;
		right: -15px;
		top: 50%;
		transform: translateY(-50%);
		height: 10px;
		width: 1px;
		background-color: $color-black;
		transition: height .5s ease;
	}

	span {
		position: relative;
		display: inline-block;
		vertical-align: top;

		&:after {
			content: "";
			position: absolute;
			left: 0;
			top: 100%;
			height: 1px;
			width: 100%;
			background-color: $color-black;
			transition: width .5s ease;
		}
	}
}

.clear {
	display: inline-block;
	vertical-align: top;
	font-weight: $font-weight-roman;
	font-size: 16px;
	line-height: 18px;
	position: relative;
	z-index: 10;
	color: $color-black;
	@include media('>=desktop') {
		font-size: 18px;
		line-height: 18px;
	}

	.no-touch & {
		&:hover {

			span {
				&:after {
					width: 0;
				}
			}
		}
	}

	&:before {
		content: "";
		position: absolute;
		right: -20px;
		top: 50%;
		transform: translateY(-50%) rotate(45deg);
		height: 1px;
		width: 11px;
		background-color: $color-black;
	}

	&:after {
		content: "";
		position: absolute;
		right: -15px;
		top: 50%;
		transform: translateY(-50%) rotate(45deg);
		height: 11px;
		width: 1px;
		background-color: $color-black;
		transition: height .5s ease;
	}

	span {
		position: relative;
		display: inline-block;
		vertical-align: top;

		&:after {
			content: "";
			position: absolute;
			left: 0;
			top: 100%;
			height: 1px;
			width: 100%;
			background-color: $color-black;
			transition: width .5s ease;
		}
	}
}