.hero-section {
	position: relative;
	z-index: 20;
	margin-top: -100px;
	color: $color-white;
	min-height: 100vh;
	display: flex;
	flex-direction: column;
	align-items: center;
	justify-content: flex-end;
	@include media('>=desktop') {
		margin-top: -150px;
	}
	@include media('>=large-desktop') {
		margin-top: -200px;
	}

	.container {
		@include media('>=desktop') {
			max-width: none;
			padding: 0 48px;
		}
	}
	.photo {
		position: absolute;
		left: 0;
		top: 0;
		width: 100%;
		height: 100%;
		z-index: -1;
		overflow: hidden;
		background: {
			size: cover;
			position: 50% 20%;
			repeat: no-repeat;
		}

		iframe {
			position: absolute;
			top: 50%;
			left: 50%;
			transform: translate(-50%, -50%);
			z-index: 1;
			min-width: 100%;
			min-height: 100%;
			border: 0;
			@media (min-aspect-ratio: 16/9) {
				height: 56.25vw;
			}

			@media (max-aspect-ratio: 16/9) {
				width: 177.78vh;
			}
			@include media('<desktop') {
				display: none;
			}
		}

		&:after {
			content: "";
			position: absolute;
			left: 0;
			top: 0;
			width: 100%;
			height: calc(100% - 100px);
			background: linear-gradient(180deg, #000000 0%, rgba(0, 0, 0, 0) 100%);
			opacity: 0.6;
			z-index: 20;
			@include media('>=desktop') {
				height: 100%;
			}
		}
		.touch & {
			iframe {
				display: none;
			}
		}
	}

	.info-holder {
		width: 100%;
		padding: 100px 0;
		@include media('>=desktop') {
			display: flex;
			align-items: center;
			justify-content: center;
			flex-grow: 1;
		}
	}

	.info-holder-wrap {
		max-width: 384px;
		@include media('>=large-desktop') {
			max-width: 590px;
		}

		.container {
			width: 100%;
			@include media('>=desktop') {
				margin: 0;
			}
		}
		p {
			@include media('>=large-desktop') {
				font-size: 30px;
				line-height: 46px;
			}
		}
	}

	.subtitle {
		text-transform: uppercase;
		font-weight: $font-weight-medium;
		font-size: 14px;
		line-height: 16px;
		margin-bottom: 5px;
		@include media('>=desktop') {
			font-size: 16px;
			line-height: 16px;
			margin-bottom: 20px;
		}
		@include media('>=large-desktop') {
			font-size: 18px;
			line-height: 18px;
		}
	}

	h1, .h1 {
		color: $color-white;
		margin-bottom: 20px;
		font-size: 30px;
		line-height: 35px;
		@include media('>=desktop') {
			font-size: 50px;
			line-height: 60px;
		}
		@include media('>=large-desktop') {
			font-size: 80px;
			line-height: 83px;
		}
	}

	.news-alert {
		margin-top: -60px;
		transform: translateY(10px);
		@include media('>=desktop') {
			margin-top: -100px;
		}
		@include media('>=large-desktop') {
			margin-top: -105px;
			transform: translateY(15px);
		}
	}
}