.representative-transactions {
	margin-bottom: 50px;
	@include media('>=desktop') {
		margin-bottom: 100px;
	}
	@include media('>=large-desktop') {
		margin-bottom: 150px;
	}

	.heading {
		padding-bottom: 30px;
		@include media('>=tablet') {
			display: flex;
			align-items: center;
			justify-content: space-between;
			padding-bottom: 30px;
			margin-bottom: 30px;
			border-bottom: 1px solid rgba($color-gray, .5);
		}
		@include media('>=desktop') {
			padding-bottom: 40px;
			margin-bottom: 50px;
		}

		h2, .h2 {
			@include media('>=tablet') {
				margin: 0;
			}
		}

		.back-link {
			@include media('>=tablet') {
				flex-shrink: 0;
				margin: 0 0 0 20px;
			}
		}
	}

	.info-list {
		padding-bottom: 40px;
		@include media('>=tablet') {
			margin-bottom: -20px;
			padding-bottom: 38px;
		}

		.item {
			padding-left: 50px;
			position: relative;
			@include media('>=tablet') {
				overflow: hidden;
			}
			@include media('>=desktop') {
				padding-left: 60px;
			}

			& + .item {
				margin-top: 30px;
				@include media('>=desktop') {
					margin-top: 44px;
				}
			}
		}

		.ico {
			width: 30px;
			height: 30px;
			display: flex;
			align-items: center;
			justify-content: center;
			background-color: $color-red-dark;
			position: absolute;
			left: 0;
			top: 0;
		}

		img {
			vertical-align: top;
			margin-bottom: 20px;
			@include media('>=tablet') {
				float: left;
				margin: 0 20px 20px 0;
				max-width: 50%;
			}
			@include media('>=desktop') {
				margin: 0 30px 30px 0;
			}
		}
	}

	.representative-transactions-slider {
		position: relative;
		padding-left: 70px;
		@include media('>=tablet') {
			margin-right: -10px;
		}
		@include media('>=desktop') {
			padding-left: 80px;
		}

		.item {
			vertical-align: top;
			@include media('>=tablet') {
				padding-right: 10px;
			}
		}

		img {
			width: 100%;
		}
	}

	.slick-arrow {
		position: absolute;
		top: 0;
		left: 0;
		width: 51px;
		height: 51px;
		background-color: $color-gray-dark;
		display: flex;
		align-items: center;
		justify-content: center;
		margin-bottom: 20px;
		transition: background-color .5s ease;
		@include media('>=desktop') {
			margin-bottom: 30px;
		}

		.no-touch & {
			&:hover {
				background-color: $color-red-dark;
			}
		}

		&.slick-prev {
			top: 70px;
			@include media('>=desktop') {
				top: 80px;
			}
		}

		svg {
			width: 13px;
			height: auto;
		}
	}
}