.cascade-blocks {
	margin-bottom: 50px;
	@include media('>=desktop') {
		margin-bottom: 80px;
	}
	@include media('>=large-desktop') {
		margin-bottom: 165px;
	}

	.block {
		margin-bottom: 50px;
		@include media('>=tablet') {
			display: flex;
			align-items: center;
		}
		@include media('>=desktop') {
			margin-bottom: 100px;
		}
		@include media('>=large-desktop') {
			margin-bottom: 150px;
		}
	}

	.image {
		background: {
			repeat: no-repeat;
			position: 50% 50%;
			size: cover;
		}
		padding-bottom: 100%;
		margin-bottom: 20px;
		position: relative;
		@include media('>=mobile-landscape') {
			padding-bottom: 48%;
		}
		@include media('>=tablet') {
			flex-shrink: 0;
			width: 50%;
		}
		@include media('>=desktop') {
			width: 53%;
			margin: 0;
		}
		@include media('>=large-desktop') {
			padding-bottom: 43%;
		}
	}

	.info {
		padding: 20px 20px 0;
		margin: 0 -20px;
		@include media('>=tablet') {
			margin: 0;
			padding: 0 0 40px 0;
		}
		@include media('>=desktop') {
			padding-left: 117px;
		}
		@include media('>=large-desktop') {
			padding: 0 0 0 125px;
		}

		p {
			margin-bottom: 15px;
			max-width: 433px;
			@include media('>=large-desktop') {
				margin-bottom: 25px;
			}
		}

		a {
			display: inline-block;
			vertical-align: middle;
			margin: 10px 0;
			@include media('>=desktop') {
				margin-top: 20px;
			}
		}
	}

	h2, .h2 {
		@include media('<desktop') {
			font-size: 32px;
			line-height: 42px;
			letter-spacing: -1px;
			margin-bottom: 15px;
		}

		span {
			color: $color-red;
		}
	}

	&:not(.invert) {
		.block {
			&:nth-child(even) {
				.image {
					@include media('>=tablet') {
						order: 2;
					}
				}

				.info {
					@include media('>=tablet') {
						padding: 0 40px 0 0;
					}
					@include media('>=desktop') {
						padding-right: 117px;
					}
					@include media('>=large-desktop') {
						padding: 0 250px 0 0;
					}
				}
			}
		}
	}

	&.invert {
		.block {
			&:nth-child(odd) {
				.image {
					@include media('>=tablet') {
						order: 2;
					}
				}

				.info {
					@include media('>=tablet') {
						padding: 40px 40px 40px 0;
					}
					@include media('>=large-desktop') {
						padding: 80px 250px 80px 0;
					}
				}
			}
		}
	}
}
