.testimonials-slider-holder {
	margin-bottom: 90px;
	position: relative;
	@include media('>=desktop') {
		margin-bottom: 120px;
	}
	@include media('>=large-desktop') {
		max-width: 1920px;
		width: 100%;
		margin: 0 auto 150px;
	}

	&:after {
		content: "";
		position: absolute;
		top: 70px;
		left: 50%;
		bottom: 0;
		transform: translateX(-50%);
		width: 105vw;
		z-index: -1;
		background-color: $color-gray-dark;
		@include media('>=large-desktop') {
			top: 125px;
		}
	}

	.container {
		@include media('>=desktop') {
			max-width: 100%;
			padding: 0;
		}
	}
}

.testimonials-slider {
	position: relative;
	z-index: 20;
	color: $color-white;
	margin: 0 -15px;

	.item {
		padding: 0 15px;
		@include media('>=tablet-landscape') {
			padding: 0;
		}
	}

	.holder {
		@include media('>=tablet') {
			display: flex;
			margin-left: -30px;
		}
	}

	.photo {
		height: 330px;
		margin-left: -15px;
		margin-bottom: 80px;
		background: {
			size: cover;
			position: 50% 50%;
			repeat: no-repeat;
		}
		@include media('>=tablet') {
			margin-bottom: 30px;
			width: calc(50% - 30px);
			margin-left: 30px;
			flex-shrink: 0;
		}
		@include media('>=tablet-landscape') {
			height: 400px;
		}
		@include media('>=desktop') {
			height: 485px;
		}
		@include media('>=medium-desktop') {
			height: 600px;
		}
		@include media('>=large-desktop') {
			height: 730px;
		}
	}

	.quote-info-holder {
		width: 100%;
		@include media('>=tablet') {
			padding-top: 120px;
		}
		@include media('>=desktop') {
			padding-top: 140px;
		}
		@include media('>=large-desktop') {
			padding-top: 260px;
		}

		.quote-holder {
			padding-top: 30px;
			@include media('>=tablet') {
				width: 100%;
				padding-top: 30px;
			}
			@include media('>=desktop') {
				padding-top: 50px;
			}
			@include media('>=large-desktop') {
				padding-top: 105px;
			}
		}
	}

	.big-title {
		margin-bottom: 0;
		padding-left: 94px;
		@include media('>=tablet') {
			padding-left: 40px;
		}
		@include media('>=large-desktop') {
			padding-left: 132px;
			font-size: 80px;
		}
	}

	.quote-holder {
		display: flex;
		align-items: flex-start;
		padding-bottom: 70px;
		position: relative;
		z-index: 10;
		overflow: hidden;
		@include media('>=tablet') {
			padding-top: 100px;
			padding-bottom: 0;
			width: auto;
			padding-right: 60px;
			margin-left: 30px;
		}
		@include media('>=tablet-landscape') {
			padding-right: 50px;
		}
		@include media('>=desktop') {
			padding-top: 140px;
			padding-right: 140px;
		}
		@include media('>=medium-desktop') {
			margin-left: 122px;
			padding-right: 204px;
		}
		@include media('>=large-desktop') {
			padding-right: 204px;
			padding-top: 220px;
		}

		> svg {
			width: 39px;
			height: auto;
			display: block;
			stroke-width: 2px;
			stroke: $color-white;
			flex-shrink: 0;
			margin: 22px 44px 0 10px;
			@include media('>=desktop') {
				width: 78px;
			}
			@include media('>=large-desktop') {
				width: 100px;
				margin-right: 50px;
			}
		}
	}

	.quote {
		border-left: 1px solid $color-white;
		padding-left: 15px;
		@include media('>=desktop') {
			padding-left: 33px;
		}
	}

	.title {
		margin-bottom: 20px;
		@include media('>=desktop') {
			font-size: 16px;
			line-height: 24px;
		}
		@include media('>=large-desktop') {
			margin-bottom: 30px;
		}
	}

	.subtitle {
		font-size: 14px;
		line-height: 16px;
		text-transform: uppercase;
		margin-bottom: 10px;
		@include media('>=desktop') {
			font-size: 16px;
			line-height: 16px;
		}
		@include media('>=large-desktop') {
			letter-spacing: 3.5px;
			margin-bottom: 20px;
		}
	}

	h2, .h2 {
		color: $color-white;
	}

	h4, .h4 {
		color: $color-white;
		@include media('>=desktop') {
			margin-bottom: 15px;
		}
	}

	blockquote {
		font-style: italic;
		position: relative;
		font-size: 16px;
		line-height: 24px;
		@include media('>=desktop') {
			font-size: 18px;
			line-height: 30px;
		}
		@include media('>=large-desktop') {
			font-size: 24px;
			line-height: 41px;
		}

		&:after {
			content: "";
			position: absolute;
			right: 100%;
			top: calc(100% - 4px);
			height: 140px;
			width: 3px;
			background-color: $color-gray-dark;
			margin-right: 14px;
			@include media('>=desktop') {
				margin-right: 31px;
			}
		}
	}

	.button {
		white-space: normal;
		margin-top: 40px;
		@include media('>=desktop') {
			margin-top: 30px;
		}
		@include media('>=large-desktop') {
			margin-top: 50px;
		}
	}
}

.clients-testimonials-print {
	display: none;
}

.testimonials-slider-nav {
	position: relative;
	z-index: 20;
	display: flex;
	align-items: center;
	justify-content: flex-end;
	padding: 0 15px;
	@include media('<tablet') {
		position: absolute;
		left: 0;
		top: 350px;
		width: 100%;
	}
	@include media('>=tablet') {
		padding-right: calc(50% + 20px);
		padding-bottom: 126px;
	}
	@include media('>=medium-desktop') {
		padding-right: calc(50% + 17px);
	}
	@include media('>=large-desktop') {
		margin-top: -13px;
	}

	.slick-arrow {
		&.slick-prev {
			margin-right: 20px;
		}

		&.slick-next {
			order: 10;
			margin-left: 20px;
		}

		svg {
			vertical-align: top;
		}
	}

	.slick-dots {
		margin-left: -15px;
		display: flex;
		align-items: center;

		li {
			margin-left: 15px;
			font-size: 0;
			line-height: 0;

			&.slick-active {
				button {
					background-color: $color-white;
				}
			}
		}

		button {
			text-indent: -9999px;
			overflow: hidden;
			position: relative;
			width: 10px;
			height: 10px;
			border: 1px solid $color-white;
			transition: background-color .5s ease;
			border-radius: 50%;
		}
	}
}