.subscribe {
	background-color: $color-red-dark;
	color: $color-white;
	font-weight: $font-weight-roman;
	padding: 20px;
	text-align: center;
	display: flex;
	align-items: center;
	justify-content: center;
	@include media('>=tablet') {
		min-height: 350px;
	}
	@include media('>=desktop') {
		font-size: 20px;
		line-height: 30px;
		min-height: 449px;
	}
	@include media('>=large-desktop') {
		font-size: 24px;
		line-height: 41px;
	}

	.box {
		max-width: 587px;
		width: 100%;
	}

	h2, .h2 {
		color: $color-white;
		@include media('>=desktop') {
			margin-bottom: 20px;
		}
	}

	form {
		padding-top: 30px;
		@include media('>=tablet') {
			display: flex;
			align-items: center;
		}
		input {
			@include media('>=large-desktop') {
				height: 51px;
			}
		}
	}

	.form-group {
		@include media('>=desktop') {
			display: flex;
			align-items: center;
			padding: 0 18px;
		}
	}

	.input-holder {
		flex-grow: 1;
	}

	.button {
		margin-top: 20px;
		@include media('>=tablet') {
			margin-top: 0;
			flex-shrink: 0;
			padding: 12px 20px;
		}
		@include media('>=large-desktop') {
			padding: 13px 24px;
		}
	}
	form{
		position: relative;
		.wpcf7-response-output {
			display: block;
			margin-top: 25px;
			font-size: 14px;
			line-height: 22px;
			@include media('>=tablet') {
				position: absolute;
				left: 0;
				top: 100%;
				width: 100%;
			}
		}
	}
}