.locations-info {
	margin-bottom: 50px;
	@include media('>=large-desktop') {
		margin-bottom: 150px;
	}
	h2, .h2 {
		text-align: center;
		margin-bottom: 40px;
		@include media('>=desktop') {
			margin-bottom: 70px;
		}
		@include media('>=large-desktop') {
			margin-bottom: 88px;
		}
	}
}

.main-offices {
	margin-bottom: 40px;
	@include media('>=tablet') {
		display: flex;
		flex-wrap: wrap;
		justify-content: space-between;
	}
	@include media('>=tablet-landscape') {
		margin-bottom: 70px;
	}
	@include media('>=large-desktop') {
		margin-bottom: 100px;
	}
	.office {
		@include media('>=tablet') {
			width: 49%;
			margin: 0 0 30px;
		}
		@include media('>=desktop') {
			margin-bottom: 50px;
		}
		@include media('>=large-desktop') {
			display: flex;
		}
	}
}

.office {
	margin-bottom: 30px;
	@include media('>=large-desktop') {
		line-height: 27px;
	}
	.image {
		min-height: 250px;
		position: relative;
		background: {
			repeat: no-repeat;
			position: 50% 50%;
			size: cover;
		}
		margin-bottom: 20px;
		@include media('>=large-desktop') {
			width: 37.5%;
			min-height: 260px;
			margin: 0 30px 0 0;
			flex-shrink: 0;
		}
		a {
			display: block;
			height: 100%;
		}
	}
	.info {
		@include media('>=tablet') {
			width: 60%;
		}
	}
	h4, .h4 {
		margin-bottom: 15px;
	}
	h6, .h6 {
		margin-bottom: 0;
	}
	address {
		font-style: normal;
		margin-bottom: 15px;
		@include media('>=tablet') {
			max-width: 300px;
		}
		p {
			margin-bottom: 0;
		}
	}
	p {
		margin-bottom: 16px;
	}
	a:not(.button) {
		@include media('>=desktop') {
			transition: color .3s;
		}
		.no-touch &:hover {
			color: $color-red;
		}
	}
}

.community-offices {
	@include media('>=mobile-landscape') {
		display: flex;
		flex-wrap: wrap;
		margin: 0 -10px;
	}
	@include media('>=large-desktop') {
		margin: 0 -120px 0 -10px;
	}
	.office {
		@include media('>=mobile-landscape') {
			width: 50%;
			padding: 0 10px;
		}
		@include media('>=tablet-landscape') {
			width: 25%;
		}
		address {
			margin-bottom: 5px;
			@include media('>=tablet') {
				max-width: 250px;
			}
		}
	}
	.directions-link {
		color: $color-gray;
		display: inline-block;
		vertical-align: top;
		margin-bottom: 20px;
		span {
			margin-right: 5px;
		}
		@include media('>=desktop') {
			margin-bottom: 30px;
		}
	}
}