.resources {
	padding-bottom: 50px;
	@include media('>=desktop') {
		padding-bottom: 100px;
	}
	@include media('>=large-desktop') {
		padding-bottom: 150px;
	}
}

.resources-list {
	margin-bottom: -30px;
	@include media('>=tablet') {
		display: flex;
		flex-wrap: wrap;
		margin-left: -30px;
	}
	@include media('>=large-desktop') {
		margin-bottom: -60px;
	}

	.item {
		padding: 10px;
		margin-bottom: 30px;
		@include media('>=tablet') {
			width: calc(50% - 30px);
			margin-left: 30px;
		}
		@include media('>=tablet-landscape') {
			width: calc(33.333% - 30px);
		}
		@include media('>=large-desktop') {
			width: calc(33.333% - 30px);
			margin-bottom: 60px;
		}
		img {
			position: absolute;
			opacity: 0;
			pointer-events: none;
		}
		&.hide {
			display: none;
		}
	}

	.photo-holder {
		position: relative;
		padding-top: 70%;
		overflow: hidden;
		margin: -10px -10px 13px;
		@include media('>=desktop') {
			padding: 0;
			height: 324px;
		}
		@include media('>=large-desktop') {
			height: 424px;
		}
	}

	.photo {
		position: absolute;
		top: 50%;
		left: 50%;
		transform: translate(-50%, -50%);
		width: 100%;
		height: 100%;
		background: {
			size: cover;
			position: 50% 50%;
			repeat: no-repeat;
		}
		transition: transform .5s ease;

		.no-touch & {
			&:hover {
				transform: translate(-50%, -50%) scale(1.1);
			}
		}

		a {
			position: absolute;
			left: 0;
			top: 0;
			width: 100%;
			height: 100%;
		}
	}

	.label {
		position: absolute;
		bottom: 10px;
		left: 8px;
		font-size: 14px;
		line-height: 14px;
		padding: 12px 20px;
		background-color: $color-white;
		display: inline-block;
		vertical-align: top;
		color: $color-black;
		text-transform: uppercase;
	}

	h5, .h5 {
		@include media('>=large-desktop') {
			margin-bottom: 10px;
		}
	}

	.date {
		color: $color-gray;
		margin-bottom: 5px;
		font-size: 16px;
		line-height: 33px;
		@include media('>=desktop') {
			font-size: 18px;
			line-height: 28px;
		}
	}

	.learn-more {
		margin-top: 20px;
		color: $color-gray;

		span:after {
			background-color: $color-gray;
		}
	}
}

.resources-slider {
	margin: 0  -15px;
	@include media('>=desktop') {
		margin-left: -20px;
		position: relative;
	}
	@include media('>=large-desktop') {
		margin-left: -30px;
	}

	&:after {
		content: "";
		position: absolute;
		right: 100%;
		top: 0;
		height: 100%;
		width: 100vw;
		background-color: $color-white;
		@include media('>=desktop') {
			margin-right: -20px;
		}
		@include media('>=large-desktop') {
			margin-right: -30px;
		}
	}

	.slick-list {
		@include media('>=desktop') {
			overflow: visible;
		}
	}

	.slick-slide {
		width: calc(100vw - 90px);
		padding-left: 15px;
		@include media('>=tablet') {
			width: 365px;
		}
		@include media('>=desktop') {
			padding-left: 20px;
		}
		@include media('>=large-desktop') {
			padding-left: 30px;
			width: 555px;
		}
	}

	.item {
		padding: 10px;
	}

	.photo-holder {
		position: relative;
		height: 174px;
		overflow: hidden;
		margin: -10px -10px 13px;
		@include media('>=tablet') {
			height: 225px;
		}
		@include media('>=large-desktop') {
			height: 428px;
		}
	}

