	.slick-arrow {
		position: absolute;
		top: 0;
		left: 15px;
		width: 50px;
		height: 50px;
		background-color: $color-gray-dark;
		display: flex;
		align-items: center;
		justify-content: center;
		margin-bottom: 20px;
		transition: background-color .5s ease;
		z-index: 40;
		@include media('>=desktop') {
			transform: translateX(-100%);
			left: -10px;
		}
		@include media('>=large-desktop') {
			left: 0;
		}

		.no-touch & {
			&:hover {
				background-color: $color-red-dark;
			}
		}

		&.slick-prev {
			@include media('>=tablet') {
				top: 80px;
			}
		}

		&.slick-next {
			left: auto;
			right: 70px;
			@include media('>=tablet') {
				right: auto;
				left: 15px;
			}
			@include media('>=desktop') {
				left: -10px;
			}
			@include media('>=large-desktop') {
				left: 0;
			}
		}

		svg {
			width: 13px;
			height: auto;
		}
	}
}