.work-benefits {
	margin-bottom: 30px;
	@include media('>=desktop') {
		margin-bottom: 80px;
	}
	@include media('>=large-desktop') {
		margin-bottom: 105px;
	}
	.container {
		@include media('>=desktop') {
			max-width: 1200px;
		}
		@include media('>=large-desktop') {
			max-width: 1854px;
		}
	}
	h2, .h2 {
		text-align: center;
		margin-bottom: 20px;
		@include media('>=large-desktop') {
			margin-bottom: 60px;
		}
	}
}

.work-benefits-list {
	@include media('>=tablet') {
		display: flex;
		flex-wrap: wrap;
		margin-left: -30px;
	}
	@include media('>=desktop') {
		margin-bottom: -30px;
	}

	.item {
		padding: 20px;
		background-color: $color-gray-dark;
		color: $color-white;
		margin-bottom: 30px;
		text-align: center;
		@include media('>=tablet') {
			display: flex;
			flex-direction: column;
			align-items: center;
			width: calc(50% - 30px);
			margin-left: 30px;
			padding: 30px;
		}
		@include media('>=tablet-landscape') {
			width: calc(33.333% - 30px);
		}
		@include media('>=desktop') {
			width: calc(25% - 30px);
			margin: 0 0 30px 30px;
			padding: 20px;
		}
		@include media('>=large-desktop') {
			min-height: 521px;
			padding: 50px 50px 40px;
		}
	}

	.icon {
		width: 60px;
		height: 60px;
		margin: 0 auto 20px;
		@include media('>=large-desktop') {
			margin-bottom: 30px;
		}
	}
	h5, .h5 {
		color: $color-white;
		font-weight: $font-weight-roman;
	}

}