.visual {
	position: relative;
	margin-bottom: 50px;
	background: {
		size: cover;
		position: 50% 50%;
		repeat: no-repeat;
	}
	color: $color-white;
	text-align: center;
	padding: 50px 0;
	@include media('>=tablet-landscape') {
		min-height: 400px;
		display: flex;
		align-items: center;
		justify-content: center;
	}
	@include media('>=desktop') {
		min-height: 500px;
		margin-bottom: 100px;
		align-items: flex-end;
		padding: 80px 0;
	}
	@include media('>=medium-desktop') {
		min-height: 600px;
	}
	@include media('>=large-desktop') {
		min-height: 960px;
		margin-bottom: 200px;
		padding: 133px 0;
	}
	@media screen and (-ms-high-contrast: active), (-ms-high-contrast: none) {
		&:after {
			content: '';
			min-height: inherit;
			font-size: 0;
		}
	}
	&:before {
		content: '';
		position: absolute;
		left: 0;
		top: 0;
		width: 100%;
		height: 100%;
		background: linear-gradient(0deg, #1C1C1C, #1C1C1C);
		opacity: 0.6;
	}
	.container {
		position: relative;
		z-index: 2;
	}
	.holder {
		max-width: 890px;
		margin: 0 auto;
	}
	h2, .h2 {
		color: $color-white;
	}
	.button {
		margin-top: 20px;
		@include media('>=desktop') {
			margin-top: 40px;
		}
	}

	&.content-bottom {
		@include media('>=desktop') {
			padding-bottom: 0;
			margin-bottom: 250px;
		}
		@include media('>=large-desktop') {
			margin-bottom: 325px;
			font-size: 24px;
			line-height: 41px;
		}
		&:before {
			display: none;
		}
		h2, .h2 {
			color: $color-black;
			@include media('>=large-desktop') {
				margin-bottom: 30px;
			}
		}
		.holder {
			background: $color-white;
			color: $color-black;
			padding: 20px;
			@include media('>=desktop') {
				padding: 50px;
				margin-bottom: -200px;
			}
			@include media('>=large-desktop') {
				max-width: 1550px;
				margin-bottom: -255px;
				padding: 50px 140px;
			}
		}
	}
}