.bio-quote {
	text-align: center;
	margin-bottom: 50px;
	@include media('>=desktop') {
		margin-bottom: 100px;
	}
	@include media('>=large-desktop') {
		margin-bottom: 150px;
	}

	h2, .h2 {
		margin-bottom: 35px;
		@include media('>=desktop') {
			margin-bottom: 70px;
		}
		@include media('>=large-desktop') {
			margin-bottom: 100px;
		}
	}

	.quote-holder {
		@include media('>=tablet') {
			display: flex;
			flex-wrap: wrap;
			margin-left: -20px;
			text-align: left;
			> div {
				width: calc(50% - 20px);
				margin-left: 20px;
			}
		}
		@include media('>=desktop') {
			margin-left: -30px;
			> div {
				width: calc(50% - 30px);
				margin-left: 30px;
			}
		}
	}

	.photo {
		padding-top: 90%;
		margin-bottom: 15px;
		background: {
			size: cover;
			position: 50% 50%;
			repeat: no-repeat;
		}
		@include media('>=tablet') {
			padding-top: 42.15%;
			margin-bottom: 0;
			//min-height: 300px;
		}
		@include media('>=tablet-landscape') {
			//min-height: 380px;
		}
	}

	.info {
		margin-bottom: 10px;
		@include media('>=tablet') {
			order: 10;
			margin-bottom: 0;
			padding-top: 20px;
		}

		a {
			.no-touch & {
				&:hover {
					text-decoration: underline;
				}
			}
		}
	}

	.name {
		margin-bottom: 5px;
		@include media('>=desktop') {
			margin-bottom: 15px;
		}

		p {
			color: $color-gray;
			text-transform: uppercase;
		}
	}

	h4, .h4 {
		margin-bottom: 5px;
	}

	.quote {
		padding: 20px;
		background-color: $color-gray-light;
		@include media('>=tablet') {
			padding: 35px;
			display: flex;
			flex-direction: column;
			justify-content: center;
		}
		@include media('>=desktop') {
			padding: 50px;
		}
		@include media('>=large-desktop') {
			padding: 77px;
		}

		svg {
			width: 30px;
			height: auto;
			display: block;
			margin-bottom: 15px;
			@include media('>=tablet') {
				margin-bottom: 30px;
			}
			@include media('>=tablet-landscape') {
				width: 60px;
			}
			@include media('>=large-desktop') {
				width: 100px;
				margin-bottom: 100px;
			}
		}

		.title {
			margin-bottom: 15px;
			@include media('>=desktop') {
				margin-bottom: 30px;
			}

			p {
				color: $color-gray;
			}
		}

		blockquote {
			font-style: italic;
			@include media('>=tablet-landscape') {
				font-size: 20px;
				line-height: 30px;
			}
			@include media('>=large-desktop') {
				font-size: 24px;
				line-height: 41px;
			}
		}
	}
}