// variables

// colors
$color-white: #fff;
$color-black: #000;
$color-red: #8D1414;
$color-red-dark: #791F1F;
$color-gray: #7A7A7A;
$color-gray-dark: #1C1C1C;
$color-gray-light: #F1F1F1;
$color-error: #ED0000;
$primary-color: $color-gray-dark;

$main-ff: 'Neue Haas Grotesk Display Pro', sans-serif;
$font-weight-roman: 500;
$font-weight-medium: 400;
$font-weight-bold: 700;

// grid settings
$gl-colCount: 24;
$gl-gridName: grid;
$gl-colName: col;
$gl-attributeName: class;
$gl-gutter: 30px; // Total left + right
$gl-gutter-vertical: 0;
$gl-mq-width: 'min-width';
$gl-mq-list: (
	xs: 320px,
	sm: 768px,
	md: 1200px,
	lg: 1800px
);

// media settings
$breakpoints: (
	mobile-landscape: 568px,
	tablet: 768px,
	tablet-landscape: 1024px,
	desktop: 1200px,
	medium-desktop: 1440px,
	large-desktop: 1800px,
	ultra-desktop: 2400px
);

$images-path: '../images/';

body {
	background: $color-white;
	color: $primary-color;
	font: $font-weight-roman 14px/23px $main-ff;
	letter-spacing: .5px;
	-webkit-font-smoothing: antialiased;
	-moz-osx-font-smoothing: grayscale;
	@include media('>=desktop') {
		font-size: 15px;
		line-height: 24px;
	}
	@include media('>=large-desktop') {
		font-size: 18px;
		line-height: 33px;
	}
}

h1, h2, h3, h4, h5, h6,
.h1, .h2, .h3, .h4, .h5, .h6 {
	color: $color-black;
	margin-bottom: 10px;

	.content & {
		padding-top: 20px;
		@include media('>=desktop') {
			padding-top: 25px;
			margin-bottom: 20px;
		}
		@include media('>=large-desktop') {
			padding-top: 30px;
		}

		&:first-child {
			padding-top: 0;
		}
	}
}

h1, .h1 {
	font-size: 30px;
	line-height: 35px;
	font-weight: $font-weight-bold;
	@include media('>=desktop') {
		font-size: 50px;
		line-height: 59px;
		letter-spacing: .5px;
	}
	@include media('>=large-desktop') {
		font-size: 80px;
		line-height: 83px;
	}
}

h2, .h2 {
	font-size: 26px;
	line-height: 35px;
	font-weight: $font-weight-bold;
	@include media('>=desktop') {
		font-size: 40px;
		line-height: 50px;
		letter-spacing: .5px;
	}
	@include media('>=large-desktop') {
		font-size: 70px;
		line-height: 83px;
	}
}

h3, .h3 {
	font-size: 20px;
	line-height: 25px;
	font-weight: $font-weight-bold;
	@include media('>=desktop') {
		font-size: 26px;
		line-height: 35px;
		letter-spacing: .5px;
	}
	@include media('>=large-desktop') {
		font-size: 45px;
		line-height: 54px;
	}
}

h4, .h4 {
	font-size: 18px;
	line-height: 24px;
	font-weight: $font-weight-medium;
	@include media('>=desktop') {
		font-size: 24px;
		line-height: 33px;
		letter-spacing: .5px;
	}
	@include media('>=large-desktop') {
		font-size: 30px;
		line-height: 33px;
	}
}

h5, .h5 {
	font-size: 16px;
	line-height: 24px;
	font-weight: $font-weight-medium;
	@include media('>=desktop') {
		font-size: 18px;
		line-height: 27px;
		letter-spacing: .5px;
	}
	@include media('>=large-desktop') {
		font-size: 26px;
		line-height: 34px;
	}
}

h6, .h6 {
	font-size: 14px;
	line-height: 20px;
	font-weight: $font-weight-medium;
	@include media('>=desktop') {
		font-size: 16px;
		line-height: 24px;
		letter-spacing: .5px;
	}
	@include media('>=large-desktop') {
		font-size: 18px;
		line-height: 24px;
	}
}

p {
	.content & {
		margin-bottom: 10px;
		@include media('>=desktop') {
			margin-bottom: 20px;
		}
	}
}

b, strong {
	font-weight: $font-weight-bold;
}

a {
	text-decoration: none;
	color: inherit;
	outline: none;

	&:hover {
	}
}

img {
	display: inline-block;
	vertical-align: top;
	max-width: 100%;
	height: auto;
}

ul {
	.content & {
		margin-bottom: 20px;
		@include media('>=large-desktop') {
			margin-bottom: 40px;
		}

		li {
			padding-left: 15px;
			margin-bottom: 15px;
			position: relative;

			&:before {
				content: "";
				float: left;
				margin: 8px 0 0 -17px;
				width: 5px;
				height: 5px;
				background: $color-red-dark;
				@include media('>=large-desktop') {
					margin-top: 13px;
				}
			}

			ul {
				margin: 20px 0;
				@include media('>=desktop') {
					margin: 15px 33px;
				}
			}

			ol {
				counter-reset: list;
				margin: 20px 0;
				@include media('>=desktop') {
					margin: 15px 33px;
				}

				li {
					padding-left: 28px;
					margin-bottom: 4px;

					&:before {
						counter-increment: list;
						content: counter(list, lower-alpha) ") ";
						position: absolute;
						left: 0;
						top: 0;
						margin: 0;
						background: none;
						width: auto;
						height: auto;
						color: $color-gray-dark;
					}
				}
			}
		}
	}
}

ol {
	.content & {
		counter-reset: counterName;
		margin-bottom: 20px;
		@include media('>=large-desktop') {
			margin-bottom: 40px;
		}

		li {
			&:before {
				counter-increment: counterName;
				content: counters(counterName, "-") ". ";
			}
		}

		ul,
		ol {
			margin: 20px 0;
			@include media('>=desktop') {
				margin: 15px 33px;
			}
		}
	}
}

// forms

input[type="text"],
input[type="password"],
input[type="email"],
input[type="tel"],
input[type="number"],
textarea {
	width: 100%;
	display: inline-block;
	margin: 0;
	border: 1px solid rgba($color-gray, .5);
	padding: 5px 15px;
	font-size: 18px;
	line-height: 30px;
	font-family: inherit;
	font-weight: $font-weight-roman;
	letter-spacing: .5px;
	height: 47px;
	vertical-align: middle;
	background: $color-white;
	outline: none;
	-webkit-appearance: none;
	-webkit-border-radius: 0;
	transition: border-color .3s;
	@include placeholder {
		color: $color-gray;
	}

	&:focus {
		border-color: $color-gray;
	}
}

.wpcf7-form-control-wrap {
	display: block;
	position: relative;
}

.wpcf7-not-valid-tip {
	font-size: 12px;
	line-height: 12px;
	color: #ED0000;
	position: absolute;
	left: 0;
	width: 100%;
	top: 100%;
	text-align: left;
	margin-top: 5px;
}

.wpcf7-response-output {
	text-align: center;
}

textarea {
	resize: none;
	height: 100px;
}

select {
	display: inline-block;
	margin: 0;
	border: 1px solid $color-black;
	padding: 5px 20px;
	line-height: 30px;
	font-size: 16px;
	height: 42px;
	vertical-align: middle;
	background: $color-white;
	outline: none;

	&:focus {
		border-color: $color-white;
	}
}

button {
	background-color: transparent;
	cursor: pointer;
	border: 0;
	padding: 0;
}

.button,
input[type="submit"] {
	display: inline-block;
	margin: 0;
	border: 1px solid transparent;
	padding: 5px 20px;
	line-height: 23px;
	font-size: 14px;
	vertical-align: middle;
	text-decoration: none;
	white-space: nowrap;
	background: $color-red;
	color: $color-white;
	cursor: pointer;
	-webkit-appearance: none;
	-webkit-border-radius: 0;
	transition: background-color .3s, color .3s;
	@include media('>=desktop') {
		font-size: 15px;
		padding: 12px 20px;
		//padding: 15px 20px;
	}
	@include media('>=large-desktop') {
		font-size: 18px;
		padding: 13px 24px;
		//padding: 18px 30px;
	}

	.no-touch &:hover {
		background-color: $color-black;
	}

	&.white {
		background-color: $color-white;
		color: $color-black;

		svg {
			vertical-align: middle;
			display: inline-block;
			margin-left: 3px;
		}

		.no-touch &:hover {
			color: $color-white;

			svg path {
				fill: $color-white;
			}
		}
	}

	&.black {
		background-color: $color-gray-dark;
		color: $color-white;

		svg {
			vertical-align: middle;
			display: inline-block;
			margin-left: 3px;
		}

		.no-touch &:hover {
			color: $color-gray-dark;
			background-color: $color-white;

			svg path {
				fill: $color-gray-dark;
			}
		}
	}

	svg {
		margin-left: 5px;

		path {
			transition: fill .5s ease;
		}
	}
}

.play-button {
	position: absolute;
	left: 50%;
	top: 50%;
	transform: translate(-50%, -50%);
	width: 70px;
	height: 70px;
	background: rgba($color-gray-dark, .8) url("#{$images-path}ico-button-play.svg") 50% 50% no-repeat;
	background-size: 16px 20px;
	border-radius: 50%;
	transition: background-color .5s ease;
	@include media('>=desktop') {
		width: 100px;
		height: 100px;
		background-size: 26px 30px;
	}

	.no-touch &:hover {
		background-color: $color-gray-dark;
	}
}

.learn-more {
	display: inline-block;
	vertical-align: top;

	.no-touch & {
		&:hover {
			span {
				&:after {
					width: 0;
				}
			}
		}
	}

	svg {
		margin-left: 5px;
	}

	span {
		position: relative;

		&:after {
			content: "";
			position: absolute;
			top: 100%;
			left: 0;
			width: 100%;
			height: 1px;
			background-color: $color-black;
			transition: width .5s ease;
			pointer-events: none;
			margin-top: 3px;
		}
	}
}

input[type="checkbox"] {
	display: inline-block;
	margin: 0;
	vertical-align: middle;
}

.checkbox-wrap {
	position: relative;
	z-index: 10;
	line-height: 20px;

	input[type="checkbox"] {
		position: absolute;
		left: 0;
		top: 0;
		width: 0;
		height: 0;

		&:checked {
			& + label,
			& + .wpcf7-list-item-label {
				&:before {
					box-shadow: inset 0 0 0 2px $color-red-dark;
					background-color: $color-red-dark;
				}

				&:after {
					opacity: 1;
				}
			}
		}

		& + label,
		& + .wpcf7-list-item-label {
			font-weight: $font-weight-roman;
			letter-spacing: 0.5px;
			line-height: 20px;
			padding-left: 35px;
			position: relative;
			vertical-align: top;
			margin-bottom: 0;
			cursor: pointer;

			.no-touch & {
				&:hover {
					&:before {
						box-shadow: inset 0 0 0 2px $color-red-dark;
					}
				}
			}

			&:before {
				content: "";
				position: absolute;
				top: -2px;
				left: 0;
				width: 20px;
				height: 20px;
				box-shadow: inset 0 0 0 2px rgba($color-gray, .2);
				cursor: pointer;
				transition: box-shadow .5s ease, background-color .5s ease;
			}

			&:after {
				content: "";
				position: absolute;
				top: -2px;
				left: 0;
				width: 20px;
				height: 20px;
				background: url("#{$images-path}ico-check.svg") 50% 50% no-repeat;
				background-size: 12px 8px;
				opacity: 0;
				transition: opacity .5s ease;
			}
		}
	}
}

input[type="radio"] {
	display: inline-block;
	margin: 0;
	vertical-align: middle;
}

label {
	display: inline-block;
	vertical-align: middle;
	font-weight: $font-weight-medium;
	margin: 0 0 10px;
	@include media('>=large-desktop') {
		font-size: 18px;
		line-height: 24px;
	}
}

blockquote {
	margin: 0;
	font-style: normal;
	padding: 0;

	p {
		margin: 0;
	}

	q {
		quotes: none;

		&:before,
		&:after {
			quotes: none;
		}
	}

	cite {
		display: block;
	}
}

table {
	margin: 0;

	th,
	td {
		border: 1px solid $color-black;
		padding: 5px;
	}
}

.column-heading {
	margin-bottom: 30px;
	@include media('>=tablet') {
		display: flex;
		align-items: flex-end;
		justify-content: space-between;
	}
	@include media('>=desktop') {
		margin-bottom: 50px;
	}

	h2, .h2 {
		@include media('>=tablet') {
			margin: 0;
		}
	}

	p {
		margin-top: 10px;
		@include media('>=desktop') {
			margin-top: 20px;
		}
	}

	.back-link {
		margin-top: 15px;
		@include media('>=tablet') {
			flex-shrink: 0;
			margin: 0 0 0 20px;
		}
	}
	.print-ico {
		flex-shrink: 0;
	}
}

.print-logo{
	display: none;
}