.accordion-item {
	z-index: 10;
	border: solid rgba($color-gray, .5);
	border-width: 1px 0;
	@include media('>=desktop') {
		padding: 10px 0 2px;
	}

	& + .accordion-item {
		border-top: 0;
		margin-top: -1px;
	}

	h5, .h5 {
		margin-bottom: 0;
		font-weight: $font-weight-roman;
		letter-spacing: 0;
		@include media('>=large-desktop') {
			font-size: 26px;
			line-height: 34px;
		}
		a {
			position: relative;
			padding: 10px 30px 10px 0;
			display: block;
			vertical-align: top;
			transition: color .5s ease;

			.no-touch & {
				&:hover {
					color: $color-red-dark;
				}
			}

			&.active {
				color: $color-red-dark;

				&:after {
					transform: rotate(180deg);
				}
			}

			&:after {
				content: "";
				position: absolute;
				top: 50%;
				margin-top: -9px;
				right: 0;
				width: 18px;
				height: 18px;
				background: url("#{$images-path}ico-accordion.svg") 50% 50% no-repeat;
				background-size: 18px 11px;
			}
		}
	}

}

.accordion-info {
	display: none;
	@include media('>=desktop') {
		margin-top: -5px;
	}

	ul {
		margin-bottom: 10px;
		@include media('>=desktop') {
		}

		li {
			@include media('>=desktop') {
				margin-bottom: 2px;
			}

			&:last-child {
				margin-bottom: 0;
			}
		}
	}
}