.info-details {
	margin-bottom: 50px;
	padding-bottom: 50px;
	border-bottom: 1px solid rgba($color-gray, .5);
	@include media('>=tablet-landscape') {
		display: flex;
		justify-content: space-between;
		align-items: flex-start;
	}
	@include media('>=large-desktop') {
		margin: 0 0 150px;
	}

	.image {
		text-align: center;
		@include media('>=tablet-landscape') {
			width: 300px;
			flex-shrink: 0;
			margin-left: 50px;
		}
		@include media('>=large-desktop') {
			width: 587px;
			min-height: 600px;
			margin-left: 185px;
		}

		img {
			@include media('>=tablet-landscape') {
				object-fit: cover;
				width: 100%;
				height: 100%;
				position: relative;
			}
		}
	}

	.category {
		padding: 12px 24px;
		background-color: $color-gray-light;
		font-size: 14px;
		line-height: 14px;
		text-transform: uppercase;
		display: inline-block;
		vertical-align: top;
		margin-bottom: 20px;
	}

	.sub-title {
		text-transform: uppercase;
		margin-bottom: 10px;
		@include media('>=desktop') {
			font-size: 24px;
			line-height: 41px;
		}
	}

	h2, .h2 {
		@include media('>=large-desktop') {
			margin-bottom: 40px;
		}
	}

	p {
		margin-bottom: 15px;
	}

	.published-info {
		margin: 30px 0;
		@include media('>=mobile-landscape') {
			display: flex;
		}
		@include media('>=large-desktop') {
			margin-top: 50px;
		}

		> div {
			margin-bottom: 20px;
			@include media('>=mobile-landscape') {
				margin: 0 50px 0 0;
			}
			@include media('>=desktop') {
				margin-right: 100px;
			}
			@include media('>=large-desktop') {
				margin-right: 170px;
			}
		}

		.title {
			color: $color-gray;
			text-transform: uppercase;
			letter-spacing: .5px;
			margin-bottom: 10px;
		}

		.social-list {
			display: flex;

			li {
				margin: 10px 10px 10px 0;
			}
		}
	}

	+ .page-holder {
		@include media('>=tablet-landscape') {
			margin-bottom: 50px;
		}
		@include media('>=large-desktop') {
			margin: -50px 0 20px;
		}
	}

	&.news-details {
		border-bottom: none;
		padding-bottom: 0;
	}
}

.back-link {
	display: inline-block;
	vertical-align: top;
	margin-bottom: 20px;
	@include media('>=desktop') {
		margin-top: -10px;
	}
	@include media('>=large-desktop') {
		margin-bottom: 75px;
	}

	svg {
		margin-right: 5px;
	}

	span {
		position: relative;
		display: inline-block;
		vertical-align: middle;

		&:after {
			content: '';
			position: absolute;
			left: 0;
			top: 100%;
			width: 100%;
			height: 1px;
			background: $color-black;
			transition: width .3s;
		}
	}
	.info-table {
		.cell {
			@include media('>=large-desktop') {
				padding: 48px 10px;
			}
		}
	}

	.no-touch &:hover {
		span {
			&:after {
				width: 0;
			}
		}
	}
}

.info-table {
	.row {
		@include media('>=mobile-landscape') {
			display: flex;
		}

		&:last-child {
			.cell {
				border: none;
			}
		}
	}

	.cell {
		border-top: 1px solid rgba($color-gray, .5);
		border-bottom: 1px solid rgba($color-gray, .5);
		padding: 20px 0;
		@include media('>=mobile-landscape') {
			flex: 1 1 0;
			padding-left: 10px;
			padding-right: 10px;
		}
		@include media('>=desktop') {
			flex: none;
			width: 40.5%;
		}
		@include media('>=large-desktop') {
			padding: 48px 10px 20px;
		}

		&:first-child {
			@include media('>=desktop') {
				padding-left: 0;
				width: 145px;
			}
		}
	}

	h5, .h5 {
		font-weight: $font-weight-roman;
		@include media('>=large-desktop') {
			font-size: 24px;
			line-height: 41px;
		}
	}

	h6, .h6 {
		margin-bottom: 5px;
		letter-spacing: 0;
	}

	address {
		font-style: normal;
		@include media('>=large-desktop') {
			line-height: 23px;
		}

		p {
			margin: 0 0 4px;
		}
	}

	p {
		margin-bottom: 15px;
		@include media('>=desktop') {
			margin-bottom: 20px;
		}
	}

	ul {
		margin-top: 10px;
	}

	li {
		position: relative;
		padding-left: 16px;
		margin-bottom: 10px;

		&:before {
			content: '';
			float: left;
			margin: 5px 0 0 -17px;
			width: 5px;
			height: 5px;
			background: $color-red;
			@include media('>=desktop') {
				margin-top: 8px;
			}
			@include media('>=large-desktop') {
				margin-top: 13px;
			}
		}
	}

	a {
		transition: color .3s;

		.no-touch &:hover {
			color: $color-red;
		}
	}
}

.directions-link {
	color: $color-gray;

	span {
		position: relative;
		display: inline-block;
		vertical-align: middle;

		&:after {
			content: '';
			position: absolute;
			left: 0;
			top: 100%;
			width: 100%;
			height: 1px;
			background: $color-gray;
			transition: width .3s;
		}
	}

	path {
		@include media('>=desktop') {
			transition: fill .3s;
		}
	}

	.no-touch &:hover {
		color: $color-gray !important;

		span {
			&:after {
				width: 0;
			}
		}
	}
}

.get-report {
	color: $color-gray;
	margin-bottom: 20px;
	@include media('>=tablet') {
		display: flex;
		justify-content: space-between;
		align-items: center;
	}
	@include media('>=large-desktop') {
		margin-top: 40px;
	}

	> div {
		@include media('>=tablet') {
			max-width: 50%;
		}
	}

	.file-size {
		margin-left: 10px;
		@include media('>=tablet') {
			margin-left: 25px;
		}
	}

	.social-list {
		display: flex;

		li {
			margin: 10px 10px 10px 0;
		}
	}
}