.get-in-touch {
	background: $color-gray-dark;
	color: $color-white;
	padding: 50px 0;
	text-align: center;
	@include media('>=desktop') {
		padding: 100px 0;
	}
	@include media('>=large-desktop') {
		padding: 150px 0;
	}

	.heading {
		margin-bottom: 30px;
		@include media('>=desktop') {
			margin-bottom: 50px;
		}
		@include media('>=large-desktop') {
			margin-bottom: 62px;
		}
	}

	h3, .h3 {
		color: $color-white;
	}

	form {
		@include media('>=desktop') {
			max-width: 1203px;
			margin: 0 auto;
		}
	}

	.form-item {
		text-align: left;
		@include media('>=tablet') {
			display: flex;
			flex-grow: 1;
			margin: 0 -10px;
			justify-content: center;
		}
		@include media('>=desktop') {
			margin: 0 -15px;
		}

		&.submit-holder {
			text-align: center;

			.form-group {
				margin-bottom: 0;
			}
		}
	}

	.form-group {
		margin-bottom: 25px;
		@include media('>=tablet') {
			width: 100%;
			flex-grow: 1;
			margin: 0 10px 25px;
		}
		@include media('>=desktop') {
			margin: 0 15px 25px;
		}
	}

	label {
		font-weight: $font-weight-medium;
		letter-spacing: 0;
		vertical-align: top;
		margin-bottom: 3px;
	}

	.button {
		@include media('>=large-desktop') {
			margin-top: 23px;
			padding: 13px 30px;
		}

		.no-touch &:hover {
			background-color: $color-red;
		}
	}
}