.page-holder {
	@include media('>=tablet-landscape') {
		display: flex;
	}

	aside {
		@include media('>=tablet-landscape') {
			flex-shrink: 0;
			width: 250px;
			margin-right: 50px;
			order: -1;
		}
		@include media('>=desktop') {
			width: 278px;
			margin-right: 111px;
		}

		.team-list {
			padding-top: 20px;
			@include media('>=tablet-landscape') {
				display: block !important;
				margin: 0 !important;
			}
			@include media('>=desktop') {
				padding-top: 40px;
			}

			.item {
				@include media('>=tablet-landscape') {
					max-width: 100% !important;
					margin-left: 0;
					margin-bottom: 40px;
				}
				@include media('>=large-desktop') {
					margin-bottom: 65px;
				}
			}

			.photo-holder {
				width: 100% !important;
				height: 245px;
			}
		}

		p {
			margin-bottom: 0;
		}
		.links {
			margin-bottom: 20px;
			@include media('>=large-desktop') {
				margin-bottom: 30px;
			}
			&:last-child {
				@include media('>=desktop') {
					padding-bottom: 10px;
				}
			}
			li {
				@include media('>=desktop') {
					margin-bottom: 7px;
				}
			}
			a {
				@include media('>=desktop') {
					transition: color .3s;
				}
				.no-touch &:hover {
					color: $color-red;
				}
			}
		}
		&.article-aside {
			margin-bottom: 50px;
			@include media('>=tablet-landscape') {
				order: 0;
				margin: 0 0 0 50px;
			}
			@include media('>=large-desktop') {
				margin-left: 185px;
			}
			.aside-info-wrap {
				&:last-child {
					border-bottom: 1px solid rgba($color-gray, .5);
				}
			}
			.speakers {
				@include media('<tablet-landscape') {
					display: none;
				}
			}
		}
	}

	.aside-info-wrap {
		border-top: 1px solid rgba($color-gray, .5);
		margin-top: 30px;
		padding-top: 30px;
		@include media('>=desktop') {
			margin-top: 50px;
			padding-top: 50px;
		}

		&:first-child {
			margin-top: 0;
		}
		p {
			margin-bottom: 15px;
			a {
				transition: color .5s;
				.no-touch &:hover {
					color: $color-red;
				}
			}
		}
	}
}

.overview-block {
	margin-bottom: 50px;
	@include media('>=desktop') {
		margin-bottom: 75px;
	}
	@include media('>=large-desktop') {
		margin-bottom: 100px;
	}
}