.bio-details {
	margin-bottom: 50px;
	@include media('>=desktop') {
		margin-bottom: 100px;
	}
	@include media('>=large-desktop') {
		margin-bottom: 150px;
	}

	.bio-details-holder {
		display: flex;
		flex-wrap: wrap;
		@include media('>=tablet-landscape') {
			flex-wrap: nowrap;
		}

		.column {
			&:nth-child(2) {
				order: -1;
				@include media('>=tablet-landscape') {
					order: 10;
					flex-shrink: 0;
					margin-left: 50px;
					width: 35%;
				}
				@include media('>=large-desktop') {
					width: 587px;
					margin-left: 183px;
				}
			}
		}
	}

	.heading {
		color: $color-gray;
		font-weight: $font-weight-roman;
		font-size: 18px;
		line-height: 22px;
		margin-bottom: 30px;
		@include media('>=desktop') {
			font-size: 22px;
			line-height: 30px;
		}
		@include media('>=large-desktop') {
			font-size: 24px;
			line-height: 41px;
			margin-bottom: 40px;
		}

		&.mobile-heading {
			@include media('>=tablet-landscape') {
				display: none;
			}
		}

		&.bio-details-heading {
			@include media('<tablet-landscape') {
				display: none;
			}
		}

		.back-link {
			font-size: 15px;
			line-height: 24px;
			color: $color-black;
			margin-bottom: 30px;
			@include media('>=large-desktop') {
				font-size: 18px;
				line-height: 33px;
			}
			@include media('>=desktop') {
				margin-bottom: 55px;
			}
			@include media('>=large-desktop') {
				margin-bottom: 75px;
			}
		}
	}

	h2, .h2 {
		@include media('>=desktop') {
			margin-bottom: 5px;
		}
	}

	.position {
		margin-bottom: 10px;
		text-transform: uppercase;
	}

	.photo-holder {
		margin-bottom: 30px;
		@include media('>=tablet') {
			max-width: 50%;
		}
		@include media('>=tablet-landscape') {
			max-width: 100%;
		}
		@include media('>=desktop') {
			margin-bottom: 50px;
		}
	}

	.bio-info {
		h5, .h5 {
			padding-top: 0;
			font-weight: $font-weight-roman;
			@include media('>=desktop') {
				margin-bottom: 5px;
			}
		}

		ul {
			@include media('>=large-desktop') {
				margin-bottom: 44px;
			}

			li {
				@include media('>=desktop') {
					margin-bottom: 2px;
				}
			}
			a {
				transition: color .5s;
				.no-touch &:hover {
					color: $color-red;
				}
			}
		}
	}

	.bio-info-column {
		@include media('>=tablet') {
			display: flex;
			margin-left: -20px;
		}
		@include media('>=large-desktop') {
			margin-left: -30px;
		}

		.wrap {
			@include media('>=tablet') {
				width: calc(50% - 20px);
				margin-left: 20px;
			}
			@include media('>=large-desktop') {
				width: calc(50% - 30px);
				margin-left: 30px;
			}
		}
	}

	.limited-information {
		.item {
			& + .item {
				padding-top: 15px;
				@include media('>=desktop') {
					padding-top: 18px;
				}
			}

			&.hide {
				display: none;
			}
		}

		h6, .h6 {
			margin-bottom: 5px;
			@include media('>=desktop') {
				margin-bottom: 0;
			}
		}

		p {
			margin-bottom: 0;
		}

		.add-more {
			margin-top: 15px;
			@include media('>=desktop') {
				margin-top: 18px;
			}
		}
	}
}

.bio-contact-box {
	margin: 30px 0;
	padding: 30px 0;
	border: solid rgba($color-gray, .5);
	border-width: 1px 0;
	@include media('>=tablet-landscape') {
		display: flex;
	}
	@include media('>=desktop') {
		margin: 40px 0 50px;
		padding: 50px 0;
	}

	.wrap {
		margin: -15px 0 0 30px;
		@include media('>=large-desktop') {
			margin-top: -20px;
			width: 279px;
		}
	}

	.title {
		font-size: 24px;
		line-height: 35px;
		@include media('>=large-desktop') {
			min-width: 124px;
		}
	}

	h6, .h6 {
		padding-top: 20px;
		margin-bottom: 5px;
		@include media('>=large-desktop') {
			padding-top: 20px;
			margin-bottom: 2px;
		}
	}

	.connect-list {
		display: flex;
		align-items: center;
		margin: 12px 0 -10px -10px;
		@include media('>=desktop') {
			margin: 12px 0 -15px -15px;
		}

		li {
			margin: 0 0 10px 10px;
			@include media('>=desktop') {
				margin: 0 0 15px 15px;
			}
		}

		a {
			display: block;
			width: 40px;
			height: 40px;
			border: 1px solid $color-red-dark;
			position: relative;

			.no-touch & {
				transition: background-color .5s ease;

				&:hover {
					background-color: $color-red-dark;

					svg path {
						fill: $color-white;
					}
				}
			}
		}

		svg {
			position: absolute;
			left: 50%;
			top: 50%;
			transform: translate(-50%, -50%);
			max-width: 16px;
			max-height: 16px;

			path {
				transition: fill .5s ease;
			}
		}
	}

	address {
		font-style: normal;
		padding-top: 3px;
		@include media('>=desktop') {
			line-height: 24px;
		}

		p + p {
			margin-top: 5px;
			@include media('>=desktop') {
				margin-top: 10px;
			}
		}
	}
}

.representative-transactions-print,
.aside-info-wrap.print {
	display: none !important;
}

@media print {
	@page {
		margin-top: 20px;
		margin-bottom: 40px;
		size: auto;
	}
	body {
		padding-top: 30px;
		padding-bottom: 60px;
		img {
			max-width: 100% !important;
		}
		.print-logo{
			display: block;
			width: 216px;
			height: 40px;
			margin: 0 auto 30px;
		}
		.main {
			padding-top: 0;
		}
		.header {
			display: none;
		}
		.back-link {
			display: none !important;
		}
		.bio-details {
			.heading.mobile-heading {
				display: none;
			}
			.bio-details-heading.heading {
				display: block;
			}
			.bio-details-holder {
				overflow: hidden;
				display: block;
				flex: none;
				.column {
					&:first-child {
						width: 60%;
						float: left;
					}
					&:last-child {
						width: 35%;
						float: right;
						.photo-holder {
							width: 100%;
							img {
								width: 100%;
							}
						}
					}
				}
				.wrap {
					margin-left: 0;
				}
				.connect-list {
					display: none;
				}
				.bio-contact-box {
					margin-top: 0;
				}
			}
			.bio-contact-box {
				padding: 10px 0 15px;
				line-height: 20px;
				h6, .h6 {
					padding: 10px 0 0;
					margin: 0;
					line-height: 20px;
				}
				p {
					margin-bottom: 5px;
				}
				address {
					line-height: 20px;
					p {
						margin: 0;
					}
				}
				.connect-wrap {
					display: none;
				}
				.wrap {
					margin: 0;
				}
			}
			.bio-info {
				ul li {
					&:before {
						margin-left: -10px;
					}
				}
			}
			.limited-information {
				.hide {
					display: block;
				}
				.add-more {
					display: none;
				}
			}
			.accordion-info {
				display: block;
				ul {
					li {
						padding-left: 0;
					}
				}
			}
			.accordion-item h5 a {
				&:after {
					display: none;
				}
			}
			.accordion-item h5 a.active {
				color: black;
			}
			.video-holder {
				display: none;
			}
		}
		.representative-transactions {
			margin-bottom: 20px;
			.heading{
				margin-bottom: 10px;
				padding: 0;
			}
			.info-list {
				margin-bottom: 20px;
				padding: 0;
				.ico {
					width: 20px;
					height: 20px;
					display: block;
					text-align: center;
				}
				.item {
					padding: 0 0 0 40px;
					+ .item{
						margin-top: 10px;
					}
					img {
						display: none;
					}
				}
			}
		}
		.representative-transactions-slider {
			display: none !important;
		}
		.representative-transactions-print {
			display: flex !important;
			flex-wrap: wrap;
			font-size: 0;
			line-height: 0;
			margin: 0;
			.item {
				display: inline-block;
				vertical-align: top;
				margin: 0 0 20px;
				padding: 0 10px;
				width: 20%;
			}
		}
		.latest-news {
			padding-bottom: 0;
			margin: 0 0 20px;
			.news-list {
				margin: 0;
				flex-wrap: nowrap;
				overflow: hidden;
				.item {
					color: black;
					padding: 0;
					margin: 0;
					background: none;
					&.hide {
						display: block;
					}
				}
				.label {
					display: none;
				}
				h5, .h5 {
					color: black;
				}
				p {
					display: none;
				}
				.learn-more {
					display: none;
				}
			}
		}
		.heading-with-button {
			margin-bottom: 10px;
			display: block;
			flex: none;
			.button {
				display: none;
			}
		}
		.resources{
			padding-bottom: 10px;
		}
		.resources-list {
			overflow: hidden;
			margin: 0;
			.item {
				margin: 0;
				padding: 0;
				.date {
					margin: 0;
				}
				.photo-holder {
					display: none;
					.photo {
						display: none;
					}
				}
				img {
					display: none;
				}
				&.hide {
					display: block;
				}
			}
			.learn-more, p {
				display: none;
			}
		}
		.upcoming-events {
			padding-bottom: 0;
			display: none;
			.events-list {
				overflow: hidden;
				margin: 0;
				.avatars {
					display: none;
				}
				.item {
					float: left;
					width: 33%;
					box-shadow: none;
					.learn-more {
						display: none;
					}
				}
			}
		}
		.contact-boxes {
			display: none;
		}
		.recognized-in {
			display: none;
		}
		.footer {
			display: none;
		}

		.add-page-menu {
			display: none;
		}
		.column-heading{
			margin-bottom: 5px;
		}
		.page-holder {
			display: block;
			flex: none;
			//overflow: hidden;
			//display: block;
			//flex: none;
			.page-content {
				//width: 65%;
				//float: left;
				h4, .h4 {
					padding-top: 10px;
					margin-bottom: 5px;
				}
				p {
					margin-bottom: 0;
				}
				.content ul {
					margin: 0;
					li {
						margin-bottom: 5px;
					}
				}
			}
			.overview-block{
				margin-bottom: 10px;
				h3:first-of-type{
					display: none;
				}
			}
			aside {
				//width: 100px;
				//float: left;
				//margin: 0;
				display: none;
			}
			.aside-info-wrap.content {
				display: none;
			}
			.aside-info-wrap.print {
				display: block !important;
				margin: 0 0 10px;
				padding: 0;
				border: none;
				.item {
					max-width: 100%;
				}
			}
			.leadership{
				margin-bottom: 10px;
				h3{
					margin-bottom: 10px;
				}
			}
		}
		.team-list {
			text-align: left;
			.item {
				text-align: left;
				width: 30%;
				display: inline-block;
				vertical-align: top;
				margin-bottom: 0;
				img {
					display: none;
					width: 100%;
					//width: 300px;
					//height: 300px;
					opacity: 1;
					position: relative;
				}
			}
			.photo-holder {
				display: none;
			}
		}
		.clients-testimonials {
			color: $color-black;
			margin: 0 0 20px;
			padding: 0;
			.h2 {
				color: $color-black !important;
			}
			blockquote {
				p {
					color: $color-black;
				}
			}
			&:after {
				display: none;
			}
			.slider-holder {
				display: none;
			}
		}
		.clients-testimonials-print {
			display: block;
			margin: 0 0 20px;
			.h2 {
				color: $color-black;
			}

			h4, .h4 {
				color: $color-black;
				@include media('>=desktop') {
					margin-bottom: 15px;
				}
			}

			blockquote {
				font-style: italic;
				position: relative;
				@include media('>=desktop') {
					font-size: 18px;
					line-height: 30px;
				}
				@include media('>=large-desktop') {
					font-size: 24px;
					line-height: 41px;
				}
			}

			.item {
				margin-bottom: 5px;
			}
			.info {
				border: none;
				padding: 0;
			}
			.heading{
				overflow: hidden;
				display: block;
				padding-bottom: 5px;
				.logo{
					width: 80px;
					float: left;
					margin-right: 10px;
				}
			}
		}
	}
}

@media print and (color) {
	* {
		-webkit-print-color-adjust: exact;
		print-color-adjust: exact;
	}
}