.info-image-section {
	margin-bottom: 50px;
	@include media('>=desktop') {
		margin-bottom: 100px;
	}
	@include media('>=large-desktop') {
		margin-bottom: 150px;
	}

	.image {
		background: {
			repeat: no-repeat;
			position: 50% 50%;
			size: cover;
		}
		height: 50vh;
		min-height: 400px;
		@include media('>=tablet-landscape') {
			height: 80vh;
		}
		@include media('>=desktop') {
			height: 100vh;
			min-height: 640px;
		}
		@include media('>=large-desktop') {
			margin-bottom: 70px;
		}
	}

	.info {
		background: $color-white;
		position: relative;
		padding: 30px 0;
		@include media('<desktop') {
			font-size: 16px;
			line-height: 24px;
		}
		@include media('>=large-desktop') {
			padding: 0;
			color: $color-gray;
		}
	}

	.holder {
		@include media('>=tablet-landscape') {
			display: flex;
			justify-content: space-between;
		}

		h2, .h2 {
			margin: -100px 0 0 -15px;
			background: $color-white;
			padding: 30px 0 30px 15px;
			@include media('>=tablet-landscape') {
				width: 52%;
				position: relative;
				z-index: 5;
				margin-top: -150px;
				padding: 50px 0;
				text-align: center;
			}
			@include media('>=desktop') {
				text-align: left;
			}
			@include media('>=large-desktop') {
				margin-top: -318px;
				padding: 70px 0;
				font-size: 80px;
				line-height: 83px;

			}

			&:before {
				content: '';
				position: absolute;
				top: 0;
				bottom: 0;
				left: -9999px;
				right: 0;
				background: $color-white;
				z-index: -1;
			}

			strong {
				display: block;
				color: $color-red-dark;
			}
		}

		.subtitle {
			text-transform: uppercase;
			font-size: 14px;
			line-height: 16px;
			font-weight: $font-weight-medium;
			display: block;
			margin-bottom: 10px;
			@include media('>=large-desktop') {
				letter-spacing: 3.5px;
				font-size: 16px;
				line-height: 20px;
			}
		}

		.description {
			@include media('>=tablet-landscape') {
				width: 50%;
			}
			@include media('>=large-desktop') {
				font-size: 24px;
				line-height: 41px;
				padding-right: 150px;
			}
		}

		p + p {
			margin-top: 20px;
		}

		.button {
			margin-top: 30px;
			@include media('>=desktop') {
				margin-top: 45px;
			}
		}
	}
}