/* scrollbars */
.jcf-scrollable-wrapper {
	box-sizing: content-box;
	position: relative;
}

.jcf-scrollbar-vertical {
	position: absolute;
	cursor: default;
	width: 6px;
	bottom: 0;
	right: 0;
	top: 0;
	border-radius: 2px;
}

.jcf-scrollbar-vertical .jcf-scrollbar-slider {
	width: 2px;
	margin: 0 auto;
	background: $color-gray-light;
}

.jcf-scrollbar-vertical .jcf-scrollbar-dec,
.jcf-scrollbar-vertical .jcf-scrollbar-inc {
	height: 0;
	width: 14px;
	left: 0;
	top: 0;
}

.jcf-scrollbar-vertical .jcf-scrollbar-inc {
	top: auto;
	bottom: 0;
}

.jcf-scrollbar-vertical .jcf-scrollbar-handle {
	background: $color-gray-dark;
	height: 1px;
	width: 3px;
	right: 0;
	border-radius: 2px;
}

.jcf-scrollbar-horizontal {
	position: absolute;
	background: #e3e3e3;
	right: auto;
	top: auto;
	left: 0;
	bottom: 0;
	width: 1px;
	height: 14px;
}

.jcf-scrollbar-horizontal .jcf-scrollbar-dec,
.jcf-scrollbar-horizontal .jcf-scrollbar-inc {
	display: inline-block;
	vertical-align: top;
	overflow: hidden;
	background: #bbb;
	height: 14px;
	width: 14px;
}

.jcf-scrollbar-horizontal .jcf-scrollbar-inc {
	left: auto;
	right: 0;
}

.jcf-scrollbar-horizontal .jcf-scrollbar-slider {
	display: inline-block;
	position: relative;
	height: 14px;
}

.jcf-scrollbar-horizontal .jcf-scrollbar-handle {
	position: absolute;
	background: #888;
	height: 14px;
}

.jcf-scrollbar.jcf-inactive .jcf-scrollbar-handle {
	visibility: hidden;
}

.jcf-scrollbar.jcf-inactive .jcf-scrollbar-dec,
.jcf-scrollbar.jcf-inactive .jcf-scrollbar-inc {
	background: #e3e3e3;
}

/* select */
.jcf-select {
	display: inline-block;
	vertical-align: top;
	position: relative;
	width: 100%;
	min-width: 150px;
	height: 47px;
	cursor: pointer;
	text-align: left;
	background-color: $color-white;
	border: 1px solid rgba(122, 122, 122, 0.5);

	&.jcf-focus {
	}

	&.jcf-compact-multiple {
		//max-width: 220px;
	}

	&.jcf-drop-active {
		border-bottom-width: 0;

		&.jcf-drop-flipped {
			//border-bottom: 1px solid rgba(20, 21, 24, 0.2);
			border-top-width: 0;
			border-bottom-width: 1px;
		}

		.jcf-select-opener {
			&:before {
				transform: translateY(-50%) rotate(180deg);
			}
		}

	}

	select {
		z-index: 1;
		left: 0;
		top: 0;
	}

	.jcf-select-text {
		font: $font-weight-roman 18px/20px $main-ff;
		text-overflow: ellipsis;
		white-space: nowrap;
		overflow: hidden;
		display: block;
		padding: 12px 30px 11px 16px;
		cursor: pointer;
		color: $color-gray;

		& > span {
			display: block;
			text-overflow: ellipsis;
			overflow: hidden;
			padding-right: 20px;
		}
	}

	.jcf-select-opener {
		position: absolute;
		text-align: center;
		width: 30px;
		bottom: 0;
		right: 0;
		top: 0;
		@include media('>=desktop') {
			width: 40px;
		}

		&:before {
			content: '';
			position: absolute;
			top: 50%;
			right: 16px;
			transform: translateY(-50%);
			width: 0;
			height: 0;
			border-style: solid;
			border-width: 8px 6px 0 6px;
			border-color: $color-black transparent transparent transparent;
			transition: transform .3s;
		}
	}

	.jcf-select-drop {
		position: absolute;
		margin-top: 0;
		z-index: 30;
		top: 100%;
		left: -1px;
		right: -1px;
	}

	.jcf-drop-flipped {
		bottom: 100%;
		top: auto;
	}
}

body > .jcf-select-drop {
	position: absolute;
	margin: -1px 0 0;
	z-index: 9999;
}

body > .jcf-select-drop.jcf-drop-flipped {
	//margin: 1px 0 0;
}

.jcf-select.jcf-compact-multiple .jcf-select-opener:before {
	//display: inline-block;
	//content: '...';
}

.jcf-select-drop {
	background: $color-white;
	border: 1px solid rgba(122, 122, 122, 0.5);
	border-top: none;

	.jcf-select-drop-content {
		border-top: none;

		.jcf-scrollbar-horizontal {
			display: none;
		}
	}

	.jcf-scrollable-wrapper {
		//padding-bottom: 15px;
	}

	.jcf-option-hideme {
		//display: none;
	}

	&.jcf-drop-flipped {
		border-color: rgba($color-black, .22);
		border-bottom: none;
		margin: 1px 0 0;

		.jcf-select-drop-content {
		}
	}
}

.jcf-select-drop.jcf-compact-multiple .jcf-hover {
	background: none;
}

.jcf-select-drop.jcf-compact-multiple .jcf-selected {

}

.jcf-select-drop.jcf-compact-multiple {
	.jcf-option {
		position: relative;
		padding-left: 30px;

		&:before {
			content: '';
			position: absolute;
			left: 0;
			top: 50%;
			width: 18px;
			height: 18px;
			border: 1px solid $color-gray;
			box-sizing: border-box;
			border-radius: 3px;
			transform: translateY(-50%);
		}

		&:after {
			content: '';
			position: absolute;
			left: 0;
			top: 50%;
			width: 19px;
			height: 16px;
			transform: translateY(-50%);
			opacity: 0;
			margin: -4px 0 0 3px;
		}
	}
}

.jcf-select-drop.jcf-compact-multiple .jcf-option.jcf-selected:after {
	opacity: 1;
}

/* multiple select styles */
.jcf-list-box {
	overflow: hidden;
	display: inline-block;
	border: 1px solid #b8c3c9;
	min-width: 200px;
	margin: 0 15px;
}

/* select options styles */
.jcf-list {
	display: block;
	position: relative;
	//margin-right: 25px;
	padding: 4px 0;

	.jcf-list-content {
		vertical-align: top;
		display: inline-block;
		overflow: auto;
		width: 100%;
	}

	ul {
		list-style: none;
		padding: 0;
		margin: 0;
	}

	li {
		overflow: hidden;
		display: block;
	}

	.jcf-overflow {
		overflow: auto;
	}

	.jcf-option {
		//white-space: nowrap;
		overflow: hidden;
		display: block;
		color: $color-gray-dark;
		transition: color .3s ease, background-color .3s ease;
		cursor: pointer;
		font-size: 16px;
		line-height: 24px;
		padding: 3px 0 3px 16px;

		&.first-disabled {
			//display: none;
		}

		&.jcf-hover,
		&.jcf-selected {
			color: $color-white;
			background: $color-red-dark;
		}

	}

	.jcf-disabled {
		display: none !important;
	}

	.jcf-optgroup-caption {
		display: block;
		cursor: default;
		padding: 0 24px;
		font-weight: bold;
		margin-bottom: 28px;
		@include media('>=desktop') {
			line-height: 20px;
		}
	}

	.jcf-optgroup {
		display: block;
		padding-bottom: 4px;

		.jcf-option {
			padding-left: 40px;
			position: relative;

			&:before {
				content: '';
				position: absolute;
				left: 25px;
				top: 8px;
				width: 6px;
				height: 6px;
				background: orange;
			}
		}
	}
}

