.menu-opened {
	@include media('<desktop') {
		overflow: hidden;
	}
}

.header {
	position: fixed;
	left: 0;
	top: 0;
	width: 100%;
	padding: 20px 0;
	background: rgba($color-black, .85);
	z-index: 100;
	transition: background-color .5s, padding .5s, box-shadow .5s;
	@include media('>=desktop') {
		background: $color-white;
		padding: 30px 0;
	}
	@include media('>=large-desktop') {
		padding: 55px 0;
	}
	&.scrolled {
		@include media('>=desktop') {
			padding: 17px 0;
			box-shadow: 4px 4px 10px rgba(0, 0, 0, 0.15);
		}
	}
	&.transparent {
		background: none;
		.main-menu {
			a {
				@include media('>=desktop') {
					color: $color-white;
				}
			}
			.arrow {
				path {
					@include media('>=desktop') {
						stroke: $color-white;
					}
				}
			}
			.sub-menu {
				a:not(:hover) {
					@include media('>=desktop') {
						color: $color-black;
					}
				}
			}
		}
		.logo {
			a:after {
				@include media('>=desktop') {
					display: none;
				}
			}
		}
		.search {
			path, circle {
				stroke: $color-white;
			}
		}
		.button {
			background: $color-white;
			color: $color-black;
			.no-touch &:hover {
				background: $color-black;
				color: $color-white;
			}
		}
	}
	&:not(transparent) {
		.main-menu {
			.arrow {
				path {
					@include media('>=desktop') {
						stroke: $color-black;
					}
				}
			}
		}
	}
	.container {
		position: relative;
		@include media('>=desktop') {
			max-width: none;
		}
	}
	.logo {
		width: 113px;
		height: 20px;
		display: block;
		position: relative;
		z-index: 11;
		@include media('>=desktop') {
			flex-shrink: 0;
			width: 216px;
			height: 40px;
		}
		@include media('>=large-desktop') {
			width: 272px;
			height: 50px;
		}
		a {
			display: block;
			height: 100%;
			position: relative;
			font-size: 0;
			line-height: 0;
			&:after {
				@include media('>=desktop') {
					content: '';
					position: absolute;
					left: 0;
					top: 0;
					width: 100%;
					height: 100%;
					background: url('#{$images-path}logo-color.svg');
					background-size: 100% 100%;
				}
			}
		}
	}
	.btn-menu {
		background: none;
		width: 30px;
		height: 30px;
		position: absolute;
		right: -8px;
		top: -4px;
		z-index: 11;
		display: inline-block;
		margin: 0;
		padding: 0 5px;
		cursor: pointer;
		text-transform: none;
		color: inherit;
		border: 0;
		font-size: 0;
		line-height: 0;
		text-align: left;
		transition: right .3s, top .3s;
		@include media('>=desktop') {
			display: none;
		}
		span {
			display: block;
			position: absolute;
			width: 17px;
			height: 2px;
			top: 50%;
			margin-top: -8px;
			transition-timing-function: ease;
			transition-duration: .4s;
			transition-property: transform, background-color;
			background-color: $color-white;
		}

		span::before,
		span::after {
			content: "";
			position: absolute;
			left: 0;
			width: 17px;
			height: 2px;
			transition-timing-function: ease;
			transition-duration: .4s;
			transition-property: transform, background-color;
			background-color: $color-white;
		}

		span::before {
			top: 7px;
			transition-timing-function: ease;
			transition-duration: .4s;
			transition-property: transform, opacity, background-color;
		}

		span::after {
			top: 14px;
		}

		&.open {
			span {
				transform: translate3d(0, 10px, 0) rotate(45deg);
				margin-top: -12px;
				&::before {
					width: 20px;
					transform: rotate(-45deg) translate3d(-5.71429px, -6px, 0);
					opacity: 0;
				}

				&::after {
					top: 20px;
					transform: translate3d(0, -20px, 0) rotate(-90deg);
				}
			}
		}
	}
	.search {
		position: absolute;
		width: 33px;
		height: 30px;
		right: 28px;
		top: -6px;
		z-index: 11;
		@include media('>=desktop') {
			right: 22px;
			top: 7px;
		}
		path, circle {
			stroke: $color-white;
			@include media('>=desktop') {
				stroke: $color-black;
				transition: stroke .3s;
			}
		}
		.no-touch &:hover {
			path, circle {
				stroke: $color-red;
			}
		}
	}
	.menu {
		position: fixed;
		left: 0;
		width: 100%;
		top: 56px;
		height: 100vh;
		height: calc((var(--vh, 1vh) * 100) - 56px);
		background: rgba($color-black, .9);
		z-index: 10;
		padding: 30px;
		visibility: hidden;
		opacity: 0;
		transform: translateX(-100%);
		display: flex;
		align-items: center;
		justify-content: center;
		flex-wrap: wrap;
		flex-direction: column;
		text-align: center;
		@include media('<desktop') {
			transition: visibility .3s, opacity .3s, transform .3s;
		}
		@include media('>=desktop') {
			background: none;
			position: relative;
			width: auto;
			top: auto;
			left: auto;
			height: auto;
			overflow: visible;
			visibility: visible;
			opacity: 1;
			transform: none;
			padding: 0 80px 0 0;
			flex-grow: 1;
		}
		@include media('>=large-desktop') {
			padding-right: 50px;
		}
		.holder {
			width: 100%;
			overflow-y: auto;
			max-height: calc(100vh - 80px);
			max-height: calc((var(--vh, 1vh) * 100) - 80px);
			padding-left: 28vw;
			text-align: left;
			@include media('>=mobile-landscape') {
				padding-left: 41%;
			}
			@include media('>=tablet') {
				padding-left: 42%;
			}
			@include media('>=tablet-landscape') {
				padding-left: 44%;
			}
			@include media('>=desktop') {
				max-height: none;
				overflow: visible;
				display: flex;
				justify-content: flex-end;
				align-items: center;
				padding: 0;
			}
		}
	}
	.button {
		@include media('<desktop') {
			background: $color-white;
			color: $color-black;
		}
		@include media('>=desktop') {
			padding: 9px 22px;
			margin-left: 44px;
		}
		@include media('>=large-desktop') {
			padding: 12px 25px;
			margin-left: 147px;
		}
	}
	.menu-opened & {
		@include media('<desktop') {
			background: $color-black;
		}
		.menu {
			visibility: visible;
			opacity: 1;
			transform: translateX(0);
		}
	}
}

.header-inner {
	position: relative;
	@include media('>=desktop') {
		display: flex;
		padding: 0 32px;
	}
	@include media('>=large-desktop') {
		padding: 0 35px;
	}
}

.main-menu {
	width: 100%;
	text-transform: capitalize;
	text-align: left;
	@include media('>=desktop') {
		display: flex;
		flex-wrap: wrap;
		justify-content: flex-end;
		width: auto;
		font-size: 15px;
		padding: 0;
	}
	@include media('>=large-desktop') {
		font-size: 16px;
	}
	> li {
		margin: 0 0 30px;
		@include media('>=desktop') {
			margin: 0 0 0 22px;
		}
		@include media('>=large-desktop') {
			margin-left: 36px;
		}
		&.menu-item-has-children {
			position: relative;
			&:before {
				@include media('>=desktop') {
					content: '';
					position: absolute;
					left: -15px;
					top: -25px;
					bottom: -25px;
					right: -15px;
					background: $color-gray-light;
					opacity: 0;
					transition: opacity .3s;
				}
				@include media('>=large-desktop') {
					left: -20px;
					right: -20px;
				}
			}
			.no-touch &:hover {
				&:before {
					opacity: 1;
				}
				> a {
					color: $color-red;
				}
				.sub-menu {
					pointer-events: auto;
					visibility: visible;
					opacity: 1;
				}
				.arrow {
					transform: rotate(180deg);
					path {
						stroke: $color-red;
					}
				}
			}
			&.active {
				.sub-menu {
					opacity: 1;
					visibility: visible;
					pointer-events: auto;
				}
				.arrow {
					transform: rotate(180deg);
				}
			}
			a {
				@include media('>=desktop') {
					position: relative;
				}
			}
		}
	}
	.current-menu-item {
		> a {
			text-decoration: underline;
		}
	}
	a {
		display: inline-block;
		vertical-align: top;
		position: relative;
		color: $color-white;
		@include media('>=desktop') {
			color: $color-black;
		}
		.no-touch &:hover {
			color: $color-red !important;
		}
	}

	.sub-menu {
		padding: 20px 0 0;
		display: none;
		@include media('>=desktop') {
			display: block !important;
			opacity: 0;
			visibility: hidden;
			transition: opacity .3s, visibility .3s;
			width: 242px;
			padding: 0;
			position: absolute;
			left: -15px;
			top: 100%;
			text-align: left;
		}
		@include media('>=large-desktop') {
			left: -20px;
		}
		> ul {
			@include media('>=desktop') {
				background: $color-gray-light;
				padding: 20px;
				margin-top: 25px;
			}
			@include media('>=large-desktop') {
				padding: 32px 20px 22px;
			}
		}
		li {
			padding: 0 0 10px;
			@include media('>=desktop') {
				border-bottom: 1px solid #C4C4C4;
				padding-bottom: 7px;
				margin-bottom: 6px;
			}
			&:last-child {
				padding-bottom: 0;
				margin-bottom: 0;
				border: none;
			}
			a {
				@include media('>=desktop') {
					color: $color-black;
				}
			}
		}
	}
	.arrow {
		width: 17px;
		display: inline-flex;
		align-items: center;
		transition: transform .3s;
		position: relative;
		cursor: pointer;
		margin-left: 10px;
		@include media('>=desktop') {
			margin-left: 5px;
		}
		svg {
			display: inline-block;
			vertical-align: middle;
			@include media('>=desktop') {
				width: 12px;
			}
		}
		path {
			transition: fill .3s;
		}
	}
}

