.job-details {
	.container {
		@include media('>=large-desktop') {
			max-width: 1230px;
		}
	}

	.heading {
		padding-bottom: 25px;
		margin-bottom: 25px;
		border-bottom: 1px solid rgba($color-gray, .5);
		@include media('>=desktop') {
			font-size: 20px;
			line-height: 30px;
			padding-bottom: 50px;
			margin-bottom: 50px;
		}
		@include media('>=large-desktop') {
			font-size: 24px;
			line-height: 41px;
		}

		h2, .h2 {
			@include media('>=desktop') {
				margin-bottom: 20px;
			}
		}

		.button {
			margin-top: 30px;
			@include media('>=desktop') {
				margin-top: 50px;
			}
		}
		.back-link{
			@include media('>=desktop') {
				font-size: 18px;
				line-height: 33px;
			}
		}
	}

	.location {
		display: flex;
		align-items: flex-end;
		margin-bottom: 15px;
		@include media('>=desktop') {
			margin-bottom: 20px;
		}
		@include media('>=large-desktop') {
			margin-bottom: 30px;
		}

		.ico {
			flex-shrink: 0;
			width: 24px;
			height: 24px;
			display: flex;
			align-items: flex-end;
			position: relative;
			top: -4px;
			margin-right: 15px;
		}

		img {
			max-width: 100%;
			max-height: 100%;
		}

		.city {
			display: block;
			font-family: $font-weight-medium;
			font-size: 18px;
			line-height: 24px;
			color: $color-gray-dark;
		}
	}

	.job-description {
		p {
			margin-bottom: 30px;
		}

		ul {
			li {
				margin-bottom: 5px;
			}
		}

		h5, .h5 {
			font-weight: $font-weight-roman;
			padding-top: 0;
			@include media('>=large-desktop') {
				margin-bottom: 15px;
			}
		}
	}
}

.job-benefits {
	padding-top: 40px;
	@include media('>=desktop') {
		padding-top: 50px;
	}

	h4, .h4 {
		margin-bottom: 20px;
		@include media('>=desktop') {
			margin-bottom: 30px;
		}
	}
}

.job-benefits-list {
	position: relative;
	@include media('>=tablet') {
		display: flex;
		flex-wrap: wrap;
		margin-left: -30px;
	}
	@include media('>=desktop') {
		padding-bottom: 40px;
	}

	&:before,
	&:after {
		content: "";
		position: absolute;
		left: 0;
		right: 0;
		height: 1px;
		background-color: rgba($color-gray, .5);
		@include media('>=tablet') {
			left: 30px;
		}
	}

	&:before {
		top: 0;
	}

	&:after {
		bottom: 0;
	}

	.item {
		padding: 20px 0;
		border-top: 1px solid rgba($color-gray, .5);
		@include media('>=tablet') {
			width: calc(50% - 30px);
			margin-left: 30px;
		}
		@include media('>=desktop') {
			padding: 30px 0;
		}

		&:first-child {
			border-top: 0;
		}

		&:nth-child(2) {
			@include media('>=tablet') {
				border-top: 0;
			}
		}
		p {
			margin: 0;
		}
		h6,.h6{
			margin-bottom: 8px;
		}
	}
}

.apply-now {
	background-color: $color-gray-light;
	padding: 50px 0;
	@include media('>=desktop') {
		padding-bottom: 100px;
	}
	@include media('>=large-desktop') {
		padding-top: 70px;
		padding-bottom: 150px;
	}

	.container {
		@include media('>=large-desktop') {
			max-width: 1230px;
		}
	}

	h3, .h3 {
		text-align: center;
		margin-bottom: 35px;
		@include media('>=desktop') {
			margin-bottom: 50px;
		}
		@include media('>=large-desktop') {
			margin-bottom: 70px;
		}
	}

	h4, .h4 {
		margin-bottom: 20px;
		@include media('>=desktop') {
			margin-bottom: 30px;
		}
	}

	input[type="text"],
	input[type="password"],
	input[type="email"],
	input[type="tel"],
	input[type="number"],
	textarea, .jcf-select {
		background-color: transparent;
	}

	.jcf-select-drop {
		background-color: $color-gray-light;
	}

	.form-column {
		@include media('>=tablet') {
			display: flex;
			flex-wrap: wrap;
			margin-left: -30px;
		}
	}

	.form-group {
		margin-bottom: 30px;
		@include media('>=tablet') {
			width: calc(50% - 30px);
			margin-left: 30px;
			margin-bottom: 30px;
		}
	}

	.attachments {
		margin-top: 20px;
		border-width: 1px 0;
		padding: 50px 0 20px;
		position: relative;
		border: solid rgba($color-gray, .5);
		border-width: 1px 0;
		@include media('>=desktop') {
			margin-top: 40px;
			padding: 70px 0 40px;
		}
	}

	.attachments-holder {
		@include media('>=tablet') {
			display: flex;
			flex-wrap: wrap;
			margin-left: -30px;
		}
	}

	.file-holder {
		display: flex;
		align-items: center;

	}

	.button-holder {
		position: relative;
		z-index: 20;
		flex-shrink: 0;
		margin-right: 20px;
		@include media('>=desktop') {
			margin-right: 30px;
		}

		input[type="file"] {
			position: absolute;
			left: 0;
			top: 0;
			width: 100%;
			height: 100%;
			opacity: 0;
			cursor: pointer;

			.no-touch & {
				&:hover {
					& + .button {
						background-color: $color-red-dark;
						color: $color-white;

						svg path {
							fill: $color-white;
						}
					}
				}
			}
		}
	}

	.file-name {
		color: $color-gray;
		font-weight: $font-weight-roman;
		max-width: 100%;
		overflow: hidden;
		text-overflow: ellipsis;
	}

	.submit-holder {
		padding-top: 30px;
		text-align: center;
		@include media('>=desktop') {
			padding-top: 50px;
		}
	}
}