
.main {
	flex-shrink: 0;
	flex-grow: 1;
	padding-top: 100px;
	@include media('>=desktop') {
		padding-top: 150px;
	}
	@include media('>=large-desktop') {
		padding-top: 200px;
	}
}

.wrapper {
	position: relative;
	overflow-x: hidden;
	width: 100%;
	@include flex(column);
	flex-shrink: 0;
	flex-grow: 1;
	min-height: 100vh;
	height: 100%;
}

.header,
.footer {
	flex-shrink: 0;
}

.container {
	padding: 0 15px;
	@include media('>=desktop') {
		margin: 0 auto;
		max-width: 1008px;
		width: 100%;
	}
	@include media('>=large-desktop') {
		max-width: 1542px;
	}
}

