.popup-open, .cookie-showed {
	overflow: hidden;
	position: relative;
	-webkit-overflow-scrolling: touch;
	height: 100%;
}

.popup {
	.popup-open & {
		opacity: 1;
		visibility: visible;
	}
}

.cookies-popup {
	&.show {
		opacity: 1;
		visibility: visible;
		-webkit-overflow-scrolling: touch;
	}
}

.popup, .cookies-popup {
	position: fixed;
	left: 0;
	top: 0;
	width: 100vw;
	height: 100vh;
	height: calc((var(--vh, 1vh) * 100));
	max-height: 100vh;
	max-height: calc((var(--vh, 1vh) * 100));
	display: flex;
	flex-wrap: wrap;
	justify-content: center;
	align-items: center;
	//overflow: auto;
	z-index: 1000;
	background-color: rgba($color-black, .6);
	opacity: 0;
	visibility: hidden;
	transition: opacity .8s ease-in-out, visibility .8s ease-in-out;

	.popup-holder {
		//display: flex;
		//align-items: center;
		//justify-content: center;
		width: 100%;
		max-height: 100vh;
		max-height: calc((var(--vh, 1vh) * 100));
		overflow: auto;
		padding: 10px;
	}

	.popup-info {
		position: relative;
		background-color: $color-white;
		padding: 20px;
		width: 100%;
		display: none;
		@include media('>=tablet') {
			max-width: 650px;
			margin: 0 auto;
			padding: 40px;
		}
		@include media('>=tablet-landscape') {
			max-width: 850px;
			padding: 75px;
		}
		@include media('>=desktop') {
			max-width: 980px;
			padding: 100px;
		}
		@include media('>=medium-desktop') {
			max-width: 1200px;
		}
		@include media('>=large-desktop') {
			padding: 150px 154px;
		}
	}

	.close {
		position: absolute;
		right: 5px;
		top: 15px;
		@include media('>=tablet') {
			right: 20px;
			top: 25px;
		}
		@include media('>=desktop') {
			top: 45px;
			right: 45px;
		}
		@include media('>=large-desktop') {
			top: 77px;
			right: 77px;
		}

		.no-touch &:hover {
			svg path {
				stroke: $color-black;
			}
		}

		svg path {
			transition: stroke .5s ease;
		}
	}
}

.subscribe-popup {
	h3, .h3 {
		text-align: center;
		padding: 0 20px;
	}

	.subtitle {
		font-size: 18px;
		line-height: 24px;
		text-align: center;
		@include media('>=desktop') {
			font-size: 22px;
			line-height: 34px;
		}
		@include media('>=large-desktop') {
			font-size: 24px;
			line-height: 41px;
		}
	}

	.topics-heading {
		padding-top: 30px;
		display: flex;
		justify-content: space-between;
		align-items: center;
		padding-bottom: 15px;
		margin-bottom: 20px;
		border-bottom: 1px solid rgba($color-gray, .5);
		@include media('>=desktop') {
			padding-top: 50px;
		}
		@include media('>=large-desktop') {
			padding-top: 70px;
		}

		label {
			margin-bottom: 0;
		}
	}

	.title {
		font-weight: $font-weight-roman;
		margin-bottom: 0;
	}

	.wpcf7-form-control.wpcf7-checkbox {
		display: block;
		padding-bottom: 40px;
		@include media('>=tablet') {
			column-count: 2;
			column-gap: 30px;
		}
		@include media('>=tablet-landscape') {
			column-count: 3;
			padding-bottom: 50px;
		}
		@include media('>=large-desktop') {
			padding-bottom: 70px;
		}
		.checkbox-wrap {
			display: block;
		}
		label {
			margin-bottom: 0;
		}
		.wpcf7-list-item {
			margin-top: 10px;
			width: 100%;
			display: inline-block;
			page-break-inside: avoid;
			@include media('>=desktop') {
				margin-top: 20px;
			}
		}

	}

	.form-group {
		margin-bottom: 20px;
		@include media('>=desktop') {
			margin-bottom: 30px;
		}
	}

	.input-columns {
		@include media('>=tablet') {
			display: flex;
			align-items: flex-end;
			margin-left: -20px;
		}
		@include media('>=desktop') {
			margin-left: -30px;
		}

		.form-group {
			@include media('>=tablet') {
				flex-grow: 1;
				flex-shrink: 1;
				flex-basis: 0;
				margin-left: 20px;
			}
			@include media('>=desktop') {
				margin-left: 30px;
			}
		}
	}

	.buttons-holder {
		display: flex;
		align-items: center;
		justify-content: center;
		padding-top: 20px;
		margin: 0 -15px;

		button {
			margin: 0 15px;
		}
		.close {
			margin: 0 30px 0 15px;
			position: relative;
			right: auto;
			top: auto;
			bottom: auto;
			left: auto;
		}
	}
}

.cookie-info {
	display: block !important;

	.title {
		margin-bottom: 30px;
		padding-right: 20px;
		@include media('>=large-desktop') {
			margin-bottom: 50px;
		}
	}

	.note {
		padding-top: 30px;
		margin-bottom: 30px;
		border: solid rgba($color-gray, .5);
		border-width: 1px 0;
		@include media('>=large-desktop') {
			padding-top: 50px;
			margin-bottom: 50px;
		}
	}

	p {
		margin-bottom: 30px;
		@include media('>=large-desktop') {
			margin-bottom: 50px;
		}
	}

	.buttons-holder {
		display: flex;
		align-items: center;
		justify-content: center;
		margin: 0 -15px;

		button {
			margin: 0 15px;

			&.clear {
				margin-right: 30px;
			}
		}
	}
}

