.latest-news {
	padding-bottom: 50px;
	@include media('>=desktop') {
		padding-bottom: 100px;
	}
	@include media('>=large-desktop') {
		padding-bottom: 150px;
	}

	.subscribe {
		margin: 80px 0 50px;
		@include media('>=tablet') {
			margin-top: 50px;
		}
		@include media('>=large-desktop') {
			margin: 100px 0;
		}
	}
}

.news-list {
	margin-bottom: -30px;
	@include media('>=tablet') {
		display: flex;
		flex-wrap: wrap;
		margin-left: -30px;
	}
	@include media('>=large-desktop') {
		margin: 0 0 -50px -52px;
	}

	.item {
		padding: 20px;
		background-color: $color-gray-dark;
		color: $color-white;
		margin-bottom: 30px;
		@include media('>=tablet') {
			display: flex;
			flex-direction: column;
			align-items: flex-start;
			width: calc(50% - 30px);
			margin-left: 30px;
			padding: 30px;
		}
		@include media('>=tablet-landscape') {
			width: calc(33.333% - 30px);
		}
		@include media('>=large-desktop') {
			width: calc(33.333% - 52px);
			margin: 0 0 50px 52px;
			padding: 50px;
		}
		&.hide {
			display: none;
		}
	}

	h5, .h5 {
		color: $color-white;
	}

	.label {
		font-size: 14px;
		line-height: 14px;
		padding: 12px 24px;
		background-color: $color-white;
		display: inline-block;
		vertical-align: top;
		color: $color-black;
		text-transform: uppercase;
		margin-bottom: 15px;
		@include media('>=large-desktop') {
			margin-bottom: 24px;
		}
	}

	.date {
		color: $color-gray;
		margin-bottom: 5px;
		font-size: 14px;
		line-height: 20px;
		@include media('>=desktop') {
			font-size: 16px;
			line-height: 33px;
			margin-bottom: 0;
		}
	}

	.box {
		color: $color-gray;
		display: flex;
		font-size: 14px;
		line-height: 20px;
		margin-bottom: 10px;
		@include media('>=large-desktop') {
			font-size: 16px;
			line-height: 33px;
		}

		span {
			margin-bottom: 0;
		}
	}

	.author {
		display: flex;
		align-items: center;

		&:before {
			content: "";
			display: block;
			width: 1px;
			height: 15px;
			background-color: $color-gray;
			margin: 0 15px;
		}
	}

	.learn-more {
		margin-top: 20px;
		@include media('>=large-desktop') {
			margin-top: 14px;
		}

		span:after {
			background-color: $color-white;
		}
	}
}