.page-title {
	background: {
		repeat: no-repeat;
		position: 50% 50%;
		size: cover;
	}
	position: relative;
	display: flex;
	align-items: flex-end;
	margin: -100px 0 100px;
	min-height: 100vh;
	@include media('>=tablet') {
		min-height: 50vh;
	}
	@include media('>=desktop') {
		min-height: 500px;
		margin-top: -150px;
	}
	@include media('>=medium-desktop') {
		min-height: 600px;
	}
	@include media('>=large-desktop') {
		min-height: 700px;
		margin-top: -200px;
	}

	&:before {
		content: '';
		position: absolute;
		left: 0;
		top: 0;
		width: 100%;
		height: 100%;
		background: linear-gradient(0deg, #1C1C1C, #1C1C1C);
		opacity: 0.6;
	}

	.container {
		flex-grow: 1;
		position: relative;
		z-index: 2;
	}

	.info {
		color: $color-white;
		padding: 100px 0;
		font-size: 18px;
		line-height: 28px;
		@include media('>=desktop') {
			max-width: 700px;
			font-size: 22px;
			line-height: 32px;
		}
		@include media('>=large-desktop') {
			max-width: 900px;
			padding-bottom: 70px;
			font-size: 24px;
			line-height: 41px;
		}
	}

	h1, .h1 {
		color: $color-white;
	}

	.button {
		margin-top: 20px;
		@include media('>=desktop') {
			margin-top: 38px;
		}
		@include media('>=large-desktop') {
			padding: 13px 30px;
		}
	}

	.back-link {
		font-size: 18px;
		line-height: 33px;
		margin-bottom: 20px;
		@include media('>=desktop') {
			margin-bottom: 30px;
		}

		svg path {
			fill: $color-white;
		}

		span {
			&:after {
				background: $color-white;
			}
		}
	}
}