body.search-results {
	.main {
		@include media('>=large-desktop') {
			padding-top: 300px;
		}
	}
}

.search-form {
	margin-bottom: 40px;
	@include media('>=desktop') {
		margin-bottom: 70px;
	}
	@include media('>=large-desktop') {
		margin-bottom: 95px;
	}

	form {
		@include media('>=tablet') {
			display: flex;
			align-items: center;
		}
	}

	.h6 {
		letter-spacing: 0;
		margin-bottom: 5px;
	}

	.input-holder {
		margin-bottom: 20px;
		@include media('>=tablet') {
			flex-grow: 1;
			margin: 0 20px 0 0;
		}
		@include media('>=desktop') {
			max-width: 1050px;
			margin-right: 30px;
		}
	}

	.button {
		@include media('>=large-desktop') {
			padding: 13px 28px;
		}
	}
}

.reset-form {
	cursor: pointer;
	display: inline-block;
	vertical-align: middle;
	background: none;
	padding: 0 20px 0 0;
	margin: 0 0 0 20px;
	border: none;
	font-family: inherit;
	font-weight: $font-weight-roman;
	font-size: 16px;
	line-height: 24px;
	letter-spacing: .5px;
	position: relative;
	@include media('>=desktop') {
		font-size: 18px;
		line-height: 33px;
		margin: -10px 0 0 30px;
		transition: color .3s;
	}

	&:before,
	&:after {
		content: '';
		position: absolute;
		right: 10px;
		top: 5px;
		height: 14px;
		width: 1px;
		background-color: $color-black;
		transition: background-color .3s;
		@include media('>=desktop') {
			top: 10px;
		}
	}

	&:before {
		transform: rotate(45deg);
	}

	&:after {
		transform: rotate(-45deg);
	}

	span {
		display: inline-block;
		vertical-align: top;
		position: relative;

		&:after {
			content: '';
			position: absolute;
			left: 0;
			top: 100%;
			width: 100%;
			height: 1px;
			background: $color-black;
			transition: background-color .3s;
		}
	}

	.no-touch &:hover {
		color: $color-red;

		&:before,
		&:after {
			background-color: $color-red;
		}

		span {
			&:after {
				background-color: $color-red;
			}
		}
	}
}

.search-filter {
	margin-bottom: 40px;
	@include media('>=tablet-landscape') {
		margin: 0 50px 0 0;
	}
	@include media('>=tablet') {
		width: 220px;
	}
	@include media('>=desktop') {
		width: 278px;
		font-size: 18px;
		margin-right: 35px;
	}

	> p {
		margin-bottom: 20px;
		@include media('>=desktop') {
			margin-bottom: 40px;
		}
	}

	ul {
		border-top: 1px solid rgba($color-gray, .5);
		@include media('>=desktop') {
			line-height: 30px;
		}
	}

	li {
		padding: 15px 0;
		position: relative;
		border-bottom: 1px solid rgba($color-gray, .5);
	}

	a {
		color: $color-gray-dark;
		display: inline-block;
		vertical-align: top;
		border-radius: 27px;
		position: relative;
		@include media('>=desktop') {
			transition: color .3s;
		}

		.no-touch &:hover {
			color: $color-red;
		}
	}

	.active {
		font-weight: $font-weight-medium;

		a {
			color: $color-red;
		}
	}
}

.search-results {
	.pagination {
		@include media('>=desktop') {
			text-align: left;
			padding: 0 22px;
		}
	}
}

.search-results-wrap {
	margin-bottom: 40px;
	@include media('>=tablet-landscape') {
		display: flex;
		@include media('>=desktop') {
			margin-bottom: 80px;
		}
	}
	@include media('>=large-desktop') {
		margin-bottom: 120px;
	}
}

.search-results {
	@include media('>=tablet-landscape') {
		flex-grow: 1;
	}

	.results {
		margin-bottom: 20px;
		@include media('>=desktop') {
			margin-bottom: 40px;
		}
	}
}

.search-results-list {
	border-top: 1px solid rgba($color-gray, .5);
	padding-top: 20px;
	margin-bottom: 40px;
	@include media('>=desktop') {
		padding-top: 30px;
	}
	@include media('>=large-desktop') {
		padding-top: 50px;
		margin-bottom: 50px;
	}

	.item {
		border-bottom: 1px solid rgba($color-gray, .5);
		padding-bottom: 20px;
		margin-bottom: 20px;
		@include media('>=tablet') {
			padding-bottom: 30px;
			margin-bottom: 30px;
		}
		@include media('>=large-desktop') {
			padding-bottom: 40px;
			margin-bottom: 45px;
			padding-right: 200px;
			position: relative;
		}
	}

	p {
		margin-bottom: 15px;
		@include media('>=desktop') {
			margin-bottom: 28px;
		}
	}

	strong {
		color: $color-red;
	}

	h4, .h4 {
		@include media('>=desktop') {
			margin-bottom: 10px;
		}

		a {
			@include media('>=desktop') {
				transition: color .3s;
			}

			.no-touch &:hover {
				color: $color-red;
			}
		}
	}

	ul {
		text-transform: uppercase;
		padding-bottom: 10px;
		@include media('>=large-desktop') {
			padding-bottom: 0;
		}

		li {
			display: inline-block;
			vertical-align: top;
			margin-bottom: 10px;
			color: $color-gray;

			&:first-child {
				&:before {
					display: none;
				}
			}

			&:before {
				content: '|';
				display: inline-block;
				vertical-align: middle;
				margin: 0 11px;
			}
		}

		a {
			color: $color-gray-dark;

			.no-touch &:hover {
				text-decoration: underline;
			}
		}
	}

	.position-description {
		max-width: 1049px;
		p {
			margin-bottom: 10px;
			@include media('>=large-desktop') {
				margin-bottom: 0;
			}
		}
	}

	.location {
		display: flex;
		align-items: flex-end;
		margin-bottom: 15px;
		@include media('>=desktop') {
			margin-bottom: 20px;
		}

		.ico {
			flex-shrink: 0;
			width: 24px;
			height: 24px;
			display: flex;
			align-items: flex-end;
			position: relative;
			top: -4px;
			margin-right: 15px;
		}

		img {
			max-width: 100%;
			max-height: 100%;
		}

		.city {
			display: block;
			font-family: $font-weight-medium;
			font-size: 18px;
			line-height: 24px;
			color: $color-gray-dark;
		}
	}

	.button {
		@include media('>=large-desktop') {
			position: absolute;
			right: 0;
			top: 0;
		}
	}
}