.contact-boxes {
	@include media('>=desktop') {
		background-color: $color-gray-dark;
	}

	.holder {
		margin: 0 -15px;
		@include media('>=tablet-landscape') {
			display: flex;
		}
	}

	.box {
		padding: 70px 15px;
		background-color: $color-gray-dark;
		text-align: center;
		color: $color-white;
		font-size: 16px;
		line-height: 24px;
		@include media('>=tablet-landscape') {
			width: 50%;
			display: flex;
			align-items: center;
			justify-content: center;
		}
		@include media('>=desktop') {
			text-align: left;
			position: relative;
			z-index: 10;
			min-height: 469px;
			padding: 70px 116px 70px 0;
			font-size: 18px;
			line-height: 30px;
		}
		@include media('>=large-desktop') {
			padding: 70px 169px 70px 0;
			font-size: 24px;
			line-height: 41px;
			min-height: 629px;
		}

		&.subscribe-box {
			background-color: $color-red-dark;
			@include media('>=desktop') {
				padding: 70px 0 70px 116px;
			}
			@include media('>=large-desktop') {
				padding: 70px 0 70px 169px;
			}

			&:after {
				@include media('>=desktop') {
					content: "";
					position: absolute;
					top: 0;
					left: 0;
					height: 100%;
					width: 50vw;
					background-color: $color-red-dark;
					z-index: -1;
				}
			}

			.button {
				.no-touch & {
					&:hover {
						background-color: $color-gray-dark;
					}
				}
			}
		}
	}

	.wrap {
		width: 100%;
		max-width: 345px;
		margin: 0 auto;
		@include media('>=tablet-landscape') {
			margin: 0;
		}
		@include media('>=desktop') {
			max-width: 373px;
		}
		@include media('>=large-desktop') {
			max-width: 587px;
		}
	}

	h2, .h2, h3, .h3 {
		color: $color-white;
		margin-bottom: 20px;
		@include media('>=desktop') {
			margin-bottom: 30px;
		}
	}

	p {
		margin-bottom: 20px;
		@include media('>=desktop') {
			margin-bottom: 30px;
		}
	}

	.button {
		margin-top: 30px;
		@include media('>=large-desktop') {
			margin-top: 50px;
		}

		.no-touch & {
			&:hover {
				background-color: $color-red-dark;
				color: $color-white;

				svg path {
					fill: $color-white;
				}
			}
		}
	}

	.email-link {
		display: inline-block;
		vertical-align: top;
		position: relative;
		font-weight: $font-weight-bold;
		@include media('>=desktop') {
			font-size: 30px;
			line-height: 33px;
		}

		&:after {
			content: '';
			position: absolute;
			left: 0;
			top: 100%;
			width: 100%;
			height: 1px;
			background: $color-white;
			margin-top: 5px;
			transition: width .3s;
		}

		.no-touch &:hover {
			&:after {
				width: 0;
			}
		}
	}

	.social-list {
		justify-content: center;
		margin-top: 20px;
		display: flex;
		@include media('>=desktop') {
			margin-top: 30px;
		}

		li {
			margin: 0 10px 10px;
		}

		a {
			border-color: $color-white;

			.no-touch &:hover {
				background: $color-white;

				path {
					fill: $color-red;
				}
			}
		}

		path {
			fill: $color-white;
			transition: fill .3s;
		}
	}

	&.align-center {
		.box {
			text-align: center;
		}
	}

	&.with-form {
		background: $color-red-dark;

		.wrap {
			max-width: 587px;
		}

		.box {
			width: 100%;
			padding: 70px 15px !important;
		}

		form {
			margin-top: 20px;
			@include media('>=tablet') {
				display: flex;
			}
			@include media('>=large-desktop') {
				margin: 30px 17px 0;
			}

			.input-holder {
				flex-grow: 1;
				@include media('<tablet') {
					margin-bottom: 20px;
				}
			}

			input {
				@include media('>=desktop') {
					height: 51px;
					padding-left: 30px;
				}
			}

			.button.black {
				margin: 0;
				@include media('>=large-desktop') {
					min-width: 182px;
				}

				.no-touch &:hover {
					background: $color-white;
					color: $color-black;

					path {
						fill: $color-black;
					}
				}
			}
		}
	}

	form {
		position: relative;
		.wpcf7-response-output {
			display: block;
			margin-top: 25px;
			font-size: 14px;
			line-height: 22px;
			@include media('>=tablet') {
				position: absolute;
				left: 0;
				top: 100%;
				width: 100%;
			}
		}
	}
}
