.article {
	margin-bottom: 40px;
	position: relative;
	@include media('>=tablet-landscape') {
		max-width: 770px;
		margin: 0 auto 50px;
	}
	@include media('>=desktop') {
		max-width: 1200px;
		margin-bottom: 70px;
	}
	@include media('>=large-desktop') {
		margin-bottom: 150px;
		font-size: 24px;
		line-height: 41px;
	}
	h1, .h1, .h2, .h2 {
		margin-bottom: 20px;
	}
	h3, .h3 {
		margin-bottom: 15px;
	}
	h4, .h4, h5, .h5, h6, .h6 {
		margin-bottom: 10px;
	}
	a {
		color: $color-red;
		text-decoration: underline;
		.no-touch &:hover {
			text-decoration: none;
		}
	}
	p {
		margin-bottom: 20px;
		@include media('>=desktop') {
			margin-bottom: 30px;
		}
		@include media('>=large-desktop') {
			margin-bottom: 45px;
		}
	}
	b, strong {
		color: $color-black;
	}
	ul {
		@include media('>=large-desktop') {
			font-size: 18px;
			line-height: 33px;
		}
	}
}