.benefits {
	margin-bottom: 50px;
	@include media('>=large-desktop') {
		margin-bottom: 150px;
	}
	.holder {
		@include media('>=tablet-landscape') {
			display: flex;
		}
	}
	h2, .h2 {
		margin-bottom: 30px;
		@include media('>=tablet-landscape') {
			width: 30%;
			padding-right: 20px;
			margin-bottom: 0;
		}
	}
	.icon {
		width: 60px;
		height: 60px;
		margin-bottom: 20px;
		@include media('>=large-desktop') {
			margin-bottom: 35px;
		}
	}
	ul {
		color: $color-black;
		@include media('>=mobile-landscape') {
			display: flex;
			flex-wrap: wrap;
			justify-content: flex-end;
			margin: 0 -10px;
		}
		@include media('>=tablet-landscape') {
			width: 70%;
		}
		@include media('>=large-desktop') {
			font-size: 26px;
			line-height: 34px;
			letter-spacing: 1px;
			padding-left: 12px;
		}
	}
	li {
		margin-bottom: 40px;
		@include media('>=mobile-landscape') {
			width: 50%;
			padding: 0 10px;
		}
		@include media('>=large-desktop') {
			padding-right: 100px;
		}
	}
}