.development-programs {
	margin-bottom: 50px;
	@include media('>=desktop') {
		margin-bottom: 97px;
	}
	h2, .h2 {
		margin-bottom: 20px;
		@include media('>=large-desktop') {
			margin-bottom: 50px;
		}
	}
	.holder {
		@include media('>=tablet-landscape') {
			display: flex;
			align-items: flex-start;
			justify-content: space-between;
		}
	}
	.nav-holder {
		@include media('>=tablet-landscape') {
			width: 30%;
		}
		@include media('>=large-desktop') {
			width: 32.5%;
		}
	}
}

.development-programs-nav {
	@include media('<tablet-landscape') {
		display: none;
	}
	border-top: 1px solid rgba($color-gray, .5);
	li {
		padding: 15px 0;
		border-bottom: 1px solid rgba($color-gray, .5);
		@include media('>=large-desktop') {
			padding: 19px 0;
		}
		&.active {
			button {
				color: $color-red;
				&:after {
					width: 100%;
				}
			}
		}
	}
	button {
		font: $font-weight-roman 14px/23px $main-ff;
		text-align: left;
		display: inline-block;
		vertical-align: middle;
		position: relative;
		@include media('>=large-desktop') {
			font-size: 24px;
			line-height: 24px;
			letter-spacing: .5px;
		}
		&:after {
			content: '';
			position: absolute;
			left: 0;
			top: 100%;
			width: 0;
			height: 1px;
			background: $color-red;
			transition: width .3s;
		}
	}
}

.development-programs-slider {
	background: $color-gray-light;
	padding: 20px;
	position: relative;
	z-index: 20;
	@include media('>=tablet-landscape') {
		width: 65%;
		background: none;
		padding: 40px;
	}
	@include media('>=desktop') {
		padding: 0;
	}
	@include media('>=large-desktop') {
		width: 59%;
	}
	&:before {
		@include media('>=tablet-landscape') {
			content: '';
			position: absolute;
			top: 0;
			left: 0;
			right: -9999px;
			bottom: 0;
			background: $color-gray-light;
			z-index: -1;
		}
	}
	&:after {
		@include media('>=tablet-landscape') {
			content: '';
			position: absolute;
			top: 0;
			left: 0;
			height: 200px;
			right: -9999px;
			background: $color-white;
			z-index: -1;
		}
		@include media('>=desktop') {
			height: 150px;
		}
		@include media('>=large-desktop') {
			height: 200px;
		}
	}
	.item {
		@include media('>=tablet-landscape') {
			padding-top: 200px;
		}
		@include media('>=desktop') {
			padding: 200px 0 50px 100px;
		}
		@include media('>=large-desktop') {
			padding: 280px 0 100px 150px;
		}
	}
	.image {
		background: {
			repeat: no-repeat;
			position: 50% 50%;
			size: cover;
		}
		padding-bottom: 60%;
		margin-bottom: 20px;
		@include media('>=tablet-landscape') {
			padding-bottom: 55%;
			margin-top: -200px;
		}
		@include media('>=large-desktop') {
			padding-bottom: 62%;
			margin-bottom: 70px;
			margin-top: -280px;
		}
	}
	h4, .h4 {
		margin-bottom: 20px;
	}
	.slick-dots {
		display: flex;
		align-items: center;
		justify-content: center;
		margin-top: 30px;
		li {
			margin: 0 7px;
			font-size: 0;
			line-height: 0;

			&.slick-active {
				button {
					background-color: $color-black;
				}
			}
		}

		button {
			text-indent: -9999px;
			overflow: hidden;
			position: relative;
			width: 10px;
			height: 10px;
			border: 1px solid $color-black;
			transition: background-color .5s ease;
			border-radius: 50%;
		}
	}
}