.upcoming-events {
	padding-bottom: 50px;
	@include media('>=desktop') {
		padding-bottom: 100px;
	}
	@include media('>=large-desktop') {
		padding-bottom: 150px;
	}
}

.events-list {
	margin-bottom: -30px;
	@include media('>=tablet') {
		display: flex;
		flex-wrap: wrap;
		justify-content: center;
	}
	@include media('>=tablet-landscape') {
		margin-left: -30px;
	}
	@include media('>=large-desktop') {
		margin: 0 0 -50px -52px;
	}

	.item {
		box-shadow: inset 0 0 0 2px $color-gray-light;
		padding: 20px;
		margin-bottom: 30px;
		@include media('>=tablet') {
			width: 60%;
		}
		@include media('>=tablet-landscape') {
			width: calc(33.333% - 30px);
			margin-left: 30px;
		}
		@include media('>=large-desktop') {
			padding: 50px;
			width: calc(33.333% - 52px);
			margin: 0 0 50px 52px;
		}
	}

	.dates-wrap {
		background-color: $color-red-dark;
		color: $color-white;
		padding: 20px;
		margin: -20px -20px 20px;
		text-transform: uppercase;
		@include media('>=large-desktop') {
			display: flex;
			align-items: center;
			justify-content: space-between;
			margin: -50px -50px 50px;
			padding: 35px 50px;
		}
	}

	.dates {
		text-align: center;
		display: flex;
		justify-content: center;
		font-size: 14px;
		line-height: 14px;
	}

	.date {
		font-weight: $font-weight-bold;
		font-size: 45px;
		line-height: 45px;
	}

	.end-date {
		padding-left: 32px;
		position: relative;

		&:before {
			content: "-";
			position: absolute;
			left: 10px;
			top: 50%;
			transform: translateY(-50%);
			font-weight: $font-weight-bold;
			font-size: 45px;
			line-height: 54px;
		}
	}

	.avatars {
		padding-top: 20px;
		display: flex;
		justify-content: center;
	}

	.photo {
		box-shadow: 0 0 0 5px $color-white;
		border-radius: 50%;
		width: 60px;
		height: 60px;
		background: {
			size: cover;
			position: 50% 50%;
			repeat: no-repeat;
		}

		& + .photo {
			margin-left: -5px;
		}
	}

	.holder {
		font-size: 14px;
		line-height: 14px;
		display: flex;
		flex-wrap: wrap;
		align-items: center;
		text-transform: uppercase;
		margin-bottom: 10px;
		@include media('>=desktop') {
			margin-bottom: 20px;
		}
		@include media('>=large-desktop') {
			margin-bottom: 30px;
		}

		a {
			padding: 12px 24px;
			background-color: $color-gray-light;
			margin-right: 20px;

		}
	}

	h5, .h5 {
		font-weight: $font-weight-roman;
		@include media('>=desktop') {
			margin-bottom: 20px;
		}
		@include media('>=large-desktop') {
			margin-bottom: 30px;
		}
	}
}

.upcoming-events-list {
	margin-bottom: -35px;
	@include media('>=tablet') {
		display: flex;
		flex-wrap: wrap;
		margin-left: -24px;
	}
	@include media('>=large-desktop') {
		margin-bottom: -60px;
		margin-left: -48px;
	}

	.item {
		padding: 10px;
		margin-bottom: 35px;
		@include media('>=tablet') {
			width: calc(50% - 24px);
			margin-left: 24px;
		}
		@include media('>=tablet-landscape') {
			width: calc(33.333% - 24px);
			margin-left: 24px;
		}
		@include media('>=large-desktop') {
			width: calc(33.333% - 48px);
			margin-left: 48px;
			margin-bottom: 60px;
		}
	}

	.photo-holder {
		position: relative;
		padding-top: 70%;
		overflow: hidden;
		margin: -10px -10px 15px;
		@include media('>=desktop') {
			padding: 0;
			height: 200px;
			margin-bottom: 20px;
		}
		@include media('>=large-desktop') {
			height: 317px;
		}
	}

	.photo {
		position: absolute;
		top: 50%;
		left: 50%;
		transform: translate(-50%, -50%);
		width: 100%;
		height: 100%;
		background: {
			size: cover;
			position: 50% 50%;
			repeat: no-repeat;
		}
		transition: transform .5s ease;

		.no-touch & {
			&:hover {
				transform: translate(-50%, -50%) scale(1.1);
			}
		}

		a {
			position: absolute;
			left: 0;
			top: 0;
			width: 100%;
			height: 100%;
		}
	}

	.label {
		position: absolute;
		bottom: 10px;
		left: 8px;
		font-size: 14px;
		line-height: 14px;
		padding: 12px 20px;
		background-color: $color-white;
		display: inline-block;
		vertical-align: top;
		color: $color-black;
		text-transform: uppercase;
	}

	h5, .h5 {
		font-weight: $font-weight-roman;
		margin-bottom: 0;

		a {
			transition: color .5s ease;

			.no-touch &:hover {
				color: $color-red-dark;
			}
		}
	}

	.box {
		display: flex;
		font-size: 14px;
		line-height: 14px;
		margin-bottom: 10px;
		@include media('>=large-desktop') {
			font-size: 16px;
			line-height: 33px;
		}
	}

	.date {
		color: $color-gray;
		flex-shrink: 0;
	}

	.categories {
		color: $color-red-dark;
		display: flex;
		align-items: center;

		&:before {
			content: "";
			display: block;
			width: 1px;
			height: 15px;
			background-color: $color-red-dark;
			margin: 0 15px;
		}

		a {
			.no-touch &:hover {
				text-decoration: underline;
			}
		}
	}

	.learn-more {
		margin-top: 20px;
		color: $color-gray;
		@include media('>=large-desktop') {
			margin-top: 30px;
		}

		span:after {
			background-color: $color-gray;
		}
	}
}
