.practice-areas {
	@include media('>=tablet-landscape') {
		margin-bottom: 100px;
	}
	@include media('>=large-desktop') {
		margin-bottom: 150px;
	}
	.holder {
		@include media('>=tablet-landscape') {
			display: flex;
		}
	}
	h2, .h2 {
		@include media('>=large-desktop') {
			margin-bottom: 38px;
		}
	}
}

.practice-areas-content {
	.practice-area-section {
		padding-top: 95px;
		@include media('>=large-desktop') {
			padding-top: 110px;
		}
		&:last-child {
			.practice-areas-list {
				@include media('>=tablet-landscape') {
					margin-bottom: 0;
				}
			}
		}
	}
}

.practice-areas-filter {
	@include media('>=tablet-landscape') {
		width: 250px;
		flex-shrink: 0;
		margin: 45px 30px 0 0;
	}
	@include media('>=desktop') {
		margin-top: 60px;
	}
	@include media('>=large-desktop') {
		width: 18.4%;
		margin-top: 120px;
	}
	.accordion {
		@include media('>=tablet-landscape') {
			width: 250px;
		}
		@include media('>=large-desktop') {
			width: 282px;
		}
	}
	.accordion-item {
		padding: 10px 0;
		h5, .h5 {
			font-weight: 400;
			@include media('>=desktop') {
				font-size: 18px;
				line-height: 24px;
			}
		}
	}
	.accordion-info {
		margin-top: 5px;
		line-height: 16px;
		@include media('>=desktop') {
			line-height: 18px;
		}
		@include media('>=large-desktop') {
			line-height: 20px;
		}
		ul {
			margin-bottom: 4px;
			li {
				margin-bottom: 12px;
				@include media('>=large-desktop') {
					margin-bottom: 15px;
				}
			}
			&.jcf-scrollable {
				@include media('>=tablet-landscape') {
					max-height: 500px;
					overflow-y: auto;
				}
				@include media('>=desktop') {
					max-height: 300px;
				}
				@include media('>=medium-desktop') {
					max-height: 400px;
				}
				@include media('>=large-desktop') {
					max-height: 500px;
				}
			}
		}
	}
	ul {
		a {
			color: $color-gray;
			.no-touch &:hover {
				text-decoration: underline;
			}
		}
	}
}

.practice-areas-list {
	border-top: 1px solid rgba($color-gray, .5);
	margin-bottom: 50px;
	padding-top: 40px;
	@include media('>=large-desktop') {
		padding-top: 50px;
		margin-bottom: 150px;
	}
	.item {
		border-bottom: 1px solid rgba($color-gray, .5);
		padding-bottom: 40px;
		margin-bottom: 40px;
		@include media('>=tablet') {
			display: flex;
		}
		@include media('>=large-desktop') {
			padding-bottom: 50px;
			margin-bottom: 50px;
		}
		&:last-child {
			border: none;
			margin-bottom: 0;
			padding-bottom: 0;
		}
	}
	.image {
		background: {
			repeat: no-repeat;
			position: 50% 50%;
			size: cover;
		}
		padding-bottom: 80%;
		margin-bottom: 20px;
		position: relative;
		@include media('>=mobile-landscape') {
			padding-bottom: 40%;
		}
		@include media('>=tablet') {
			flex-shrink: 0;
			width: 200px;
			height: 170px;
			padding: 0;
			margin: 0 30px 0 0;
		}
		@include media('>=large-desktop') {
			width: 280px;
			height: 230px;
		}
		a {
			position: absolute;
			left: 0;
			top: 0;
			width: 100%;
			height: 100%;
		}
	}
	p {
		margin-bottom: 10px;
		@include media('>=large-desktop') {
			margin-bottom: 43px;
		}
	}
	.description {
		@include media('>=large-desktop') {
			display: flex;
		}
	}
	h4, .h4 {
		margin-bottom: 16px;
	}
	.focus-areas {
		margin-top: 20px;
		@include media('>=large-desktop') {
			width: 278px;
			flex-shrink: 0;
			margin: 0 0 0 30px;
		}
		ul {
			margin: 0 0 -10px;
		}
		li {
			display: inline-block;
			vertical-align: top;
			margin: 0 10px 10px 0;
			@include media('>=large-desktop') {
				line-height: 24px;
				display: block;
			}
			&:before {
				float: none;
				position: absolute;
				left: 0;
				top: 50%;
				transform: translateY(-50%);
				margin: 0;
			}
		}
		a {
			transition: color .5s;
			.no-touch &:hover {
				color: $color-red;
			}
		}
	}
	h5, .h5 {
		font-weight: $font-weight-roman;
		margin-bottom: 15px;
	}
	.jcf-scrollable {
		padding-left: 2px;
		@include media('>=large-desktop') {
			height: 230px;
		}
	}
}

.sticky-element-wrap {
	position: relative;
}