.media-section {
	margin-bottom: 50px;
	@include media('>=desktop') {
		margin-bottom: 100px;
	}
	@include media('>=large-desktop') {
		margin-bottom: 150px;
	}

	.heading {
		margin-bottom: 35px;
		text-align: center;
		font-size: 16px;
		line-height: 24px;
		@include media('>=tablet-landscape') {
			max-width: 750px;
			width: 100%;
			margin: 0 auto 50px;
			font-size: 20px;
			line-height: 30px;
		}
		@include media('>=large-desktop') {
			margin-bottom: 70px;
			font-size: 24px;
			line-height: 41px;
			max-width: 895px;
		}

		h2, .h2 {
			@include media('>=desktop') {
				margin-bottom: 20px;
			}
		}
	}
}

.video-holder {
	padding-top: 56.25%;
	position: relative;
	z-index: 10;

	&.video-play {
		.poster {
			opacity: 0;
			visibility: hidden;
		}
	}

	.content & {
		margin: 30px 0;
		@include media('>=desktop') {
			margin: 40px 0 50px;
		}
	}

	iframe {
		position: absolute;
		left: 0;
		top: 0;
		width: 100%;
		height: 100%;
	}

	.poster {
		position: absolute;
		left: 0;
		top: 0;
		width: 100%;
		height: 100%;
		background: {
			size: cover;
			position: 50% 50%;
			repeat: no-repeat;
		}
		transition: opacity .5s, visibility .5s;
	}
}