.pagination {
	padding: 0 30px;
	text-align: center;
	position: relative;

	.link {
		@include media('>=desktop') {
			position: absolute;
			right: 0;
			top: 0;
			margin: 0;
		}
		@include media('>=desktop') {
			margin: -7px 5px 0 0;
		}
	}

	&.top-indent {
		&:before {
			content: "";
			display: block;
			padding-top: 50px;
			@include media('>=desktop') {
				padding-top: 100px;
			}
		}
	}

	&.with-border {
		&:before {
			content: "";
			display: block;
			height: 1px;
			margin: 0 -30px 25px;
			padding-top: 40px;
			border-bottom: 1px solid rgba($color-gray, .5);
			@include media('>=desktop') {
				padding-top: 100px;
				margin-bottom: 50px;
			}
		}
	}

	.wp-pagenavi {
		display: inline-flex;
		align-items: center;
		justify-content: center;
		flex-wrap: wrap;
		position: relative;
		width: 100%;
		@include media('>=desktop') {
			width: auto;
			padding: 0 35px;
		}

		a, span {
			display: flex;
			align-items: center;
			justify-content: center;
			width: 30px;
			height: 30px;
			margin: 0 2px 10px;
			text-decoration: none;
			font-size: 16px;
			line-height: 28px;
			color: $color-gray;
			border: 1px solid rgba($color-gray, .3);
			position: relative;
			font-weight: $font-weight-medium;
			@include media(">=desktop") {
				width: 40px;
				height: 40px;
				margin: 0 15px;
			}

			svg {
				flex-shrink: 0;
			}
		}

		a {
			.no-touch & {
				transition: color .3s, border-color .3s;

				&:hover {
					color: $color-red;
					border-color: $color-red;
				}
			}
		}

		.current {
			border-color: $color-red;
			color: $color-red;
		}

		.previouspostslink,
		.nextpostslink {
			position: absolute;
			top: 50%;
			width: 30px;
			height: 30px;
			margin-top: -20px;
			@include media('>=desktop') {
				width: 40px;
				height: 40px;
				margin-top: -20px;
			}

			&.disabled {
				opacity: .5;
				pointer-events: none;
			}

			path {
				transition: fill .3s;
			}

			.no-touch &:hover {
				border-color: $color-red;

				path {
					fill: $color-red;
				}
			}
		}

		.previouspostslink {
			transform: rotate(180deg);
			left: 0;
			margin-left: -30px;
			@include media('>=desktop') {
				margin-left: -20px;
			}
		}

		.nextpostslink {
			right: 0;
			margin-right: -30px;
			@include media('>=desktop') {
				margin-right: -20px;
			}
		}
	}
}